  getRainGaugByid(myData: any) {
    if (myData) {
      console.log('TEST', myData);

      const newData = myData.split(",");
      this.defaultStationName = newData[1];
      this.spinner.show();
      const data = {
        station_id: newData[0],
        type: newData[2]
      };
      console.log('TEST', data);

      this.getRainGaugeByMinute(newData[0], newData[2])
      this.webServices.rainGaugeForgraph(data).subscribe((res) => {
        console.log('TEST', res);

        this.spinner.hide();
        if (res.data.length > 0) {
          this.rainGaugeList = res.data;
          this.selectFomatShow('8')
          this.manageDataChart();
          this.manageDataTable(7);
        } else {
          this.AlertNOhavedata();
        }
      }),
        (err) => {
          console.log(err);
          this.AlertNOhavedata();
        };
    }
  }

