  getRainGaugeByMinute(stationId: number, type: string) {
    const data = {
      station_id: stationId,
      type: type
    }
    console.log('TEST', data);

    this.webServices.rainGaugeForgraphByMinute(data).subscribe
      (res => {
        console.log("get rain gauge by station id ", res);
        this.setDataForPdf(res.data)
        this.data_Minute = res.data
        if (res.data.length > 0) {
          const dataGraph = res.data
          this.manageDataChart2(dataGraph)
        } else {
          this.AlertNOhavedata()
        }
      }), errr => {
        console.log(errr);
      }
  }
