import { Component, OnInit, ViewChild } from "@angular/core";
import { Subscription } from "rxjs";
import { StationService } from "../../services/station.service";
import Swal from "sweetalert2";
import { SynoticService } from "../../services/synotic.service";
import * as moment from "moment";
import { ForwebAPIService } from "../../services/forweb-api.service";
import { NgxSpinnerService } from "ngx-spinner";
import * as Highcharts from "highcharts";
import * as htmlToImage from 'html-to-image';

@Component({
  selector: "app-forcastshownow",
  templateUrl: "./forcastshownow.component.html",
  styleUrls: ["./forcastshownow.component.scss"],
})
export class ForcastshownowComponent implements OnInit {
  public page: number = 1;
  public limit: number;
  public keyword: string = "";
  public datenow: any = moment().format("llll");
  public dataChart: Array<any> = [];
  public dataTimeChart: Array<any> = [];

  public airtemparater: number;

  public temp: number;
  public subs: Array<Subscription> = [];
  public station_data: any;

  public synoticById_data: any;
  public chartOptions: any;

  public featureNmae: string = ''
  public featureName_lao: string = ''

  public featureColor: string = ''
  public featuruTooltrip: string = ''

  public datagraph: any
  public checkLanguse: string = ''
  public checkLanguse22: boolean = false
  public mmtype: string = '';
  highcharts = Highcharts;
  title = "myHighchart";
  public data = this.dataChart;
  constructor(
    // private synoticservice: SynoticService,
    private webServices: ForwebAPIService,
    private spinner: NgxSpinnerService
  ) { }

  ngOnInit(): void {
    this.getListall();
    // this.charthight();
    this.checkLanguage()
  }

  checkLanguage() {
    const local = localStorage.getItem('language')
    if (local == 'eng') {
      this.checkLanguse = 'eng'
      this.checkLanguse22 = true
      this.mmtype = 'mm'
      console.log('tests', this.mmtype);

      console.log('this.checkLanguse', this.checkLanguse);
    } else {
      this.checkLanguse = 'lao'
      this.mmtype = ''
      console.log('tests', this.mmtype);

      this.checkLanguse22 = false

      console.log('this.checkLanguse', this.checkLanguse);

    }
  }

  generateImage() {
    console.log('tetsttststtststst');
    htmlToImage.toJpeg(document.getElementById('card-graph'), { quality: 0.95 })
      .then(function (dataUrl) {
        var link = document.createElement('a');
        link.download = 'my-image-name.jpeg';
        link.href = dataUrl;
        link.click();
      });

  }
  ngOnDestroy() {
    this.subs.forEach((v) => {
      v.unsubscribe();
    });
  }
  getListall() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
    };
    this.subs.push(
      this.webServices.getSynotic_listAll(data).subscribe(
        (res) => {
          // console.log(res);
          if (res.status == 1) {
            this.station_data = res.data;
            const stationId = res.data[0].id
            console.log('my station', this.station_data);
            this.loadfristBYid(stationId);

          } else {
            this.AlertNOhavedata();
          }
        },
        (err) => {
          console.log(err);
        }
      )
    );
  }
  getidstation(myid: string) {
    console.log("myid", myid);
  }

  getsinoticById(myid: string) {
    this.spinner.show();
    const data = {
      limit: (this.limit = 3),
      page: this.page,
      station_id: myid,
    };
    this.webServices.getSynoticBy_stationId(data).subscribe((res) => {
      this.spinner.hide();

      if (res.status == 1) {
        this.synoticById_data = res.data.Data[0];
        const station_id = res.data.Data[0].station_id;
        this.loaddataForGraph(station_id);
        console.log("ress data", this.synoticById_data);
      } else {
        this.AlertNOhavedata();
        this.spinner.hide();
      }
    }),
      (err) => {
        console.log(err);
      };
  }

  loadfristBYid(station_Id: number) {
    this.spinner.show();

    const data = {
      limit: (this.limit = 3),
      page: this.page,
      station_id: station_Id,
    };
    this.webServices.getSynoticBy_stationId(data).subscribe((res) => {
      this.spinner.hide();

      if (res.status == 1) {
        this.synoticById_data = res.data.Data[0];
        // console.log("ress data", this.synoticById_data);
        const stationId = res.data.Data[0].station_id;
        this.loaddataForGraph(stationId);
      } else {
        this.AlertNOhavedata();
      }
    }),
      (err) => {
        console.log(err);
      };
  }

  loaddataForGraph(station_id: number) {
    console.log('gfuhsdbfuhsdbfiuyvsdfyudsvfiuydsvfyusvf');

    const data = {
      limit: (this.limit = 1),
      page: this.page,
      station_id: station_id,
    };
    this.webServices.getSynoticBy_Graph(data).subscribe(
      (res) => {
        console.log("resr graph data", res);
        // this.manageDataChart(res.data);
        this.datagraph = res.data
        this.setFeatureForGraph('air_temperature')

      },
      (error) => {
        console.log(error);
      }
    ),
      (eer) => {
        console.log(eer);
      };
  }

  setFeatureForGraph(feature: string) {
    console.log('===========feature=======', feature);

    const data = this.datagraph
    this.manageDataChart(data, feature);

  }
  manageDataChart(data: any, feature: string) {
    this.dataChart = [];
    this.dataTimeChart = [];

    for (let index = 0; index < data.length; index++) {
      const myIndex = data.length - (index + 1);
      const element = data[myIndex];
      // console.log('element', element);

      const airTemp = element[feature] === "" || element[feature] === "E6" ? 0 : element[feature];
      const airTemparature = parseFloat(airTemp);
      const dateTime = element["time"];
      let check = moment(dateTime, "YYYY/MM/DD H:mm");
      let hours = check.format("H");
      let minute = check.format("mm");
      if (hours.length <= 1) hours = "0" + hours;
      if (minute.length <= 1) minute = "0" + minute;

      const myTime = hours + ":" + minute;
      this.dataChart.push(airTemparature);
      this.dataTimeChart.push(myTime);
    }

    // console.log("this.dataChart", this.dataChart);
    // console.log("dataTimeChart", this.dataTimeChart);

    this.charthight(feature);
  }
  AlertNOhavedata() {
    Swal.fire({
      icon: "error",
      title: "Oops...",
      text: " !",
      // footer: '<a href="">Why do I have this issue?</a>'
    });
  }

  setInt(data: any) {
    const temp = Math.ceil(data);
    return temp;
  }

  // graph

  charthight(feature: string) {
    const check = this.checkLanguse22
    const myfeature = feature
    if (myfeature === "air_temperature") {
      if (this.checkLanguse22 === true) {
        this.featureNmae = 'air_temperature'
        this.featureName_lao = 'Graph Temperature'
      } else {
        this.featureNmae = ''
        this.featureName_lao = ''
      }
      this.featuruTooltrip = ' C'
      this.featureColor = '#F3844C'
    } else if (myfeature === "avg_wind_speed") {
      if (this.checkLanguse22 === true) {
        this.featureNmae = 'avg_wind_speed'
        this.featureName_lao = 'Graph Average wind speed'
      } else {
        this.featureNmae = ''
        this.featureName_lao = ''
      }
      this.featuruTooltrip = 'm/s'
      this.featureColor = '#33C0FD'
    } else if (myfeature === "rain_fall") {
      if (this.checkLanguse22 === true) {
        this.featureNmae = 'rain_fall'
        this.featureName_lao = 'Graph Rain'
      } else {
        this.featureNmae = ''
        this.featureName_lao = ' '
      }

      this.featuruTooltrip = 'mm'

      this.featureColor = '#305FFA'
    } else if (myfeature === "air_pressure") {
      if (this.checkLanguse22 === true) {
        this.featureNmae = 'air_pressure'
        this.featureName_lao = 'Graph Air pressure'
      } else {
        this.featureNmae = ''
        this.featureName_lao = ''
      }

      this.featuruTooltrip = 'hPa'

      this.featureColor = '#AC0103'

    }
    let mydata = [
      {
        name: this.featureNmae,
        color: this.featureColor,

        marker: {
          symbol: "square",
        },
        data: this.dataChart,
      },
    ];
    const opption = {

      chart: {
        // type: "column",
        type: "spline",
        zoomType: 'xy',
        style: {
          fontFamily: 'boonhom'
        }

      },
      title: {
        text: this.featureName_lao,
        style: {
          fontFamily: 'boonhom',
          color: this.featureColor,

        }
      },
      xAxis: {
        categories: this.dataTimeChart,
        crosshair: true,
      },
      // legend: {
      //   layout: "vertical",
      //   align: "right",
      //   verticalAlign: "middle",
      // },


      yAxis: {
        title: {
          text: this.featureNmae,
          style: {
            fontFamily: 'boonhom',
            color: this.featureColor,
          }
        },

        labels: {
          style: {
            fontFamily: 'boonhom',
            color: this.featureColor,
          },
          formatter: function () {
            if (myfeature === "air_temperature") {
              return this.value + " C";
            } else if (myfeature === "avg_wind_speed") {

              return this.value + "m/s";
            } else if (myfeature === "rain_fall") {
              if (check === true) {
                console.log('5555555555555', this.checkLanguse22);

                return this.value + " mm ";

              } else {
                return this.value + "  ";

              }

            } else if (myfeature === "air_pressure") {

              return this.value + "hPa";

            }
          },
        },
      },

      tooltip: {
        valueSuffix: this.featuruTooltrip
      },
      // plotOptions: {
      //   spline: {
      //     stacking: 'normal',
      //     // dataLabels: {
      //     //   enabled: true,
      //     //   format: '${y}M',
      //     //   color: 'black',
      //     //   style: {
      //     //     fontSize: 16,
      //     //     fontWeight: ''
      //     //   },
      //     //   x: 95,
      //     // }
      //   }
      // },
      series: mydata,
    };
    this.chartOptions = opption;
  }

  //ending chart
}
