import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { APIService } from "./api.service";
@Injectable({
  providedIn: "root",
})
export class WaterserviceService extends APIService {
  public url: string = this.getBaseUrl();
  public token = "";

  getList_waterStation(param: any): Observable<any> {
    const header = this.headerBase("rainGaugeChilldrenOfStation");
    return this.http.post(this.url + "rainGauge.api.php", param, {
      headers: header,
    });
  }
  getRainGaugeListDataBYid(param: any): Observable<any> {
    const header = this.headerBase("rainGaugeListByStation");
    return this.http.post(this.url + "forWeb.api.php", param, {
      headers: header,
    });
  }
  getGrahp_waterStation(param: any): Observable<any> {
    const header = this.headerBase("rainGaugeListForGraph");
    return this.http.post(this.url + "rainGauge.api.php", param, {
      headers: header,
    });
  }

  // get by hours
  getGrahp_sum(param: any): Observable<any> {
    const header = this.headerBase("rainGaugeSumHours");
    return this.http.post(this.url + "rainGauge.api.php", param, {
      headers: header,
    });
  }

  //get by 10 minut

  getGrahp_minute(param: any): Observable<any> {
    const header = this.headerBase("rainGaugeSumMinutes");
    return this.http.post(this.url + "rainGauge.api.php", param, {
      headers: header,
    });
  }
  getReport_waterStation(param: any): Observable<any> {
    const header = this.headerBase("rainGaugeChilldrenOfStation");
    return this.http.post(this.url + "rainGauge.api.php", param, {
      headers: header,
    });
  }

  getstationList_waterStation(param: any): Observable<any> {
    const header = this.headerBase("stationRainGaugeListAllByUser");
    return this.http.post(this.url + "station.api.php", param, {
      headers: header,
    });
  }

  //export excell
  reportExelByOption(param: any): Observable<any> {
    const header = this.headerBase("reportRainGaugeChildrenStation");
    return this.http.post(this.url + "report.api.php", param, {
      headers: header,
    });
  }
  // export csv
  reportCSVByOption(param: any): Observable<any> {
    const header = this.headerBase("reportRainGaugeByAnyStationAndTime");
    return this.http.post(this.url + "report.api.php", param, {
      headers: header,
    });
  }
}
