import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';


@Component({
  selector: 'app-menutop2',
  templateUrl: './menutop2.component.html',
  styleUrls: ['./menutop2.component.scss']
})
export class Menutop2Component implements OnInit {

  @Input() Active: string;
  @Output() keyword: EventEmitter<string> = new EventEmitter
  constructor() { }

  ngOnInit(): void {
    console.log(this.Active)
  }

  onsearching(event: KeyboardEvent) {
    this.keyword.emit((event.target as HTMLInputElement).value)
    console.log('key seacrh', (event.target as HTMLInputElement).value);
  }

}
