import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import Swal from "sweetalert2";
import { ReportfilecsvService } from "../../services/reportfilecsv.service";
import { XlsxService } from "../../services/xlsx.service";

@Component({
  selector: "app-authok-exportdetail",
  templateUrl: "./authok-exportdetail.component.html",
  styleUrls: ["./authok-exportdetail.component.scss"],
})
export class AuthokExportdetailComponent implements OnInit {
  @ViewChild("myDiv") myDiv: ElementRef;
  public startTime: string;
  public endTime: string;
  public array_id: Array<any> = [];
  public excelfile = ["Cabbage", "Turnip", "Radish", "Carrot"];

  public station_id: Array<any> = [];
  public gydrology_data_report: Array<any>;
  public status: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    public reportservice: ReportfilecsvService,
    public xlsx: XlsxService
  ) { }

  ngOnInit(): void {
    this.loading();
    this.getReportListhydrologyByAnyStationAndTime();
  }

  loading() {
    this.route.queryParams.subscribe((params) => {
      this.station_id = params.stationId;
      this.endTime = params.endTime;
      this.startTime = params.startTime;
      this.status = params.status;
    });
  }

  getReportListhydrologyByAnyStationAndTime() {
    let id = this.station_id;
    const data = {
      // station_id: "1,2,3",
      station_id: id.map(Number).toString(),
      // startTime: "2021-05-16 00:00:00",
      startTime: this.startTime + " 00:00:00",
      // endTime: "2021-05-16 10:59:59"
      endTime: this.endTime + " 23:59:59",
    };
    console.log("Send data to server", data);

    this.reportservice
      .gethydrologyListByAnyStationAndTime(data)
      .subscribe((res) => {
        console.log(res);
        if (res.status == 1) {
          // this.synotic_data_report = res.data;
          if (this.status == "csv") {
            this.router.navigate(["/climate-report"]);
            this.xlsx.downloadFileCSV(res.data);
          } else {
            this.router.navigate(["/climate-report"]);
            // this.xlsx.exportToExcelFile(res.data);
          }
        } else {
          this.router.navigate(["/climate-report"]);
          Swal.fire({
            icon: "warning",
            title: "!",
            text: " ",
            showConfirmButton: true,
          });
        }
      }),
      (err) => {
        console.log(err);
      };
  }
}
