import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";

@Component({
  selector: "app-water-menu",
  templateUrl: "./water-menu.component.html",
  styleUrls: ["./water-menu.component.scss"],
})
export class WaterMenuComponent implements OnInit {
  @Input() Active: string;
  @Output() keyword: EventEmitter<string> = new EventEmitter();
  constructor() {}

  ngOnInit(): void {
    console.log(this.Active);
  }

  onsearching(event: KeyboardEvent) {
    this.keyword.emit((event.target as HTMLInputElement).value);
    console.log("key seacrh", (event.target as HTMLInputElement).value);
  }
}
