import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import * as moment from "moment";
import { StationService } from "../../services/station.service";
import { SynoticService } from "../../services/synotic.service";
import {
  WindRose,
  WindDescription,
  Service,
} from "../../services/chartservices";

@Component({
  selector: "app-forecastestaion",
  templateUrl: "./forecastestaion.component.html",
  styleUrls: ["./forecastestaion.component.scss"],
  providers: [Service],
})
export class ForecastestaionComponent implements OnInit {
  public isCollapsed: boolean = false;
  windRoseData: WindRose[];
  windSources: WindDescription[];

  public limit: number = 6;
  public page: number = 1;
  public keyword: string = "";

  //data
  public stationList_data: Array<ModelstationLis>;
  public datalist: any = {};
  public NameStation: string;
  constructor(
    private router: Router,
    // private serviceGetstation: HydrologyService,
    private synoticservice: SynoticService,
    private stationList: StationService,
    public service: Service
  ) {
    this.windRoseData = service.getWindRoseData();
    this.windSources = service.getWindSources();
  }
  public laotime: any = moment.locale("lo");
  public dateNow: any = moment().format("LLLL");

  ngOnInit(): void {
    this.loadingPage();
  }

  onLegendClick(e: any) {
    var series = e.target;
    if (series.isVisible()) {
      series.hide();
    } else {
      series.show();
    }
  }
  loadingPage() {
    this.getStationList();
    this.getSynopticListBystation();
    this.getSynopticListPage();
  }
  getStationList() {
    const userId = JSON.parse(localStorage.getItem("userData"));

    const data = {
      userid: userId.id,
      limit: this.limit,
      page: this.page,
      keyword: this.keyword,
    };
    console.log("get station by getStationAllByUser", data);

    this.stationList.getStationAllByUser(data).subscribe((res) => {
      this.stationList_data = res.data;
      console.log("get station by getStationAllByUserr", this.stationList_data);
    }),
      (err) => {
        console.log(err);
      };
  }
  getSynopticListBystation() {
    const station_id = 1;
    const data = {
      station_id: station_id,
      limit: this.limit,
      page: this.page,
      keyword: this.keyword,
    };
    console.log("get synotic", data);

    this.synoticservice.getSynoticBystation(data).subscribe((res) => {
      this.datalist = res.data;
      console.log("getSynotic by  station", this.datalist);
    }),
      (err) => {
        console.log(err);
      };
  }

  getSynopticListPage() {
    const data = {
      limit: (this.limit = 10),
      page: this.page,
      keyword: this.keyword,
    };
    console.log("get synotic", data);

    this.synoticservice.getSynoticPage(data).subscribe((res) => {
      this.datalist = res.data;
      console.log("getSynotic by  page", this.datalist);
    }),
      (err) => {
        console.log(err);
      };
  }
  getsynoticBystation(ev: any) {
    const data = {
      limit: (this.limit = 10),
      page: this.page,
      station_id: this.NameStation,
    };
    console.log("get station sonitic by station", data);
    this.synoticservice.getSynoticBystation(data).subscribe((res) => {
      this.datalist = res.data;
      console.log("getSynotic by  getSynoticBystation", this.datalist);
    }),
      (err) => {
        console.log(err);
      };
  }
  onsearching(event: KeyboardEvent) {
    this.keyword = (event.target as HTMLInputElement).value;
    console.log("key seacrh", this.keyword);

    this.getSynopticListPage();
  }
}

export class ModelstationLis {
  alt: number;
  id: number;
  lat: number;
  lng: number;
  name_eng: string;
  name_lao: string;
  part: string;
  station_model: string;
  station_number: string;
  station_phonenumber: number;
  type: string;
}
