  exportExcelBysheet(data: Array<ReportList>) {
    let myDate = moment().format("YYYY-MM-DD H:m:s");

    let station = [];
    let array_data = [];

    console.log("data report", data);
    for (let index = 0; index < data.length; index++) {
      const element = data[index];
      station.push(element.name_eng);
      array_data.push(element.children);
    }

    console.log("data report", array_data);

    const fileName = "Data" + myDate + ".xlsx";

    const sheetName = station;

    let wb = XLSX.utils.book_new();
    for (var i = 0; i < sheetName.length; i++) {
      let ws = XLSX.utils.json_to_sheet(array_data[i]);
      let csv = XLSX.utils.sheet_to_csv(ws);
      XLSX.utils.book_append_sheet(wb, ws, sheetName[i]);
    }

    XLSX.writeFile(wb, fileName);
  }
