

import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { APIService } from "./api.service";
@Injectable({
  providedIn: 'root'
})
export class SynoticService extends APIService {
  public url: string = this.getBaseUrl();
  public token = '';


  getSynoticBystation(param: any): Observable<any> {
    const header = this.headerBase('synopticListByStation');
    return this.http.post(this.url + 'synoptic.api.php', param, { headers: header });
  }
  getSynoticPage(param: any): Observable<any> {
    const header = this.headerBase('synopticListPage');
    return this.http.post(this.url + 'synoptic.api.php', param, { headers: header });
  }
  getSynoticPageBypart(param: any): Observable<any> {
    const header = this.headerBase('synopticListPageByPart');
    return this.http.post(this.url + 'synoptic.api.php', param, { headers: header });
  }


  getSynoticForGraph(param: any): Observable<any> {

    const header = this.headerBase('synopticListForGraph');
    return this.http.post(this.url + 'synoptic.api.php', param, { headers: header });
  }
}


