import { Component, OnInit } from "@angular/core";
import * as Highcharts from "highcharts";
import * as moment from "moment";
import { NgxSpinnerService } from "ngx-spinner";
import { Subscription } from "rxjs";
import Swal from "sweetalert2";
import { ForwebAPIService } from "../../services/forweb-api.service";
import * as htmlToImage from 'html-to-image';

@Component({
  selector: "app-forcastshownow-hydrology",
  templateUrl: "./forcastshownow-hydrology.component.html",
  styleUrls: ["./forcastshownow-hydrology.component.scss"],
})
export class ForcastshownowHydrologyComponent implements OnInit {
  public subs: Array<Subscription> = [];
  public station_data: any;
  updateFlag = false;
  oneToOneFlag = true;
  public hydrologyList: Array<any>;
  public dataChart: Array<any> = [];
  public rain_data: Array<any> = [];

  public lineDanger: Array<any> = [];
  public lineWarning: Array<any> = [];
  public dataTimeChart: Array<any> = [];

  public stationName: string;
  public lastUpdate: string;
  public lastWaterLevel: number;
  public lastWaterTemperature: string;
  public lastRainFall: string;

  public numberWarning: number;
  public numberDanger: number;
  public graph_Name: string = '';
  public warnning: string = '';
  public dangerlevel: string = '';
  public waterLevel: string = '';
  public rainFall: string = '';
  public mmType: string = ''
  public mType: string = ''

  public Last1HourRain: string
  highcharts = Highcharts;
  public chartOptions: any;
  public checkLangguuage: boolean = false
  constructor(
    private spinner: NgxSpinnerService,
    private webServices: ForwebAPIService
  ) { }

  ngOnInit(): void {
    this.getStationHydrology();
    this.checkLanguge()
  }

  checkLanguge() {
    const localstore = localStorage.getItem('language')
    if (localstore === 'eng') {
      this.checkLangguuage = true
      this.graph_Name = 'Hydrogy Graph'
      this.warnning = 'Warning Level '
      this.dangerlevel = 'Danger Level '
      this.waterLevel = 'Water Level '
      this.rainFall = 'Rain Fall '
      this.mmType = '( mm )'
      this.mType = '( m )'

    } else {
      this.graph_Name = ''
      this.warnning = ' '
      this.dangerlevel = ' '
      this.waterLevel = ''
      this.rainFall = ' '
      this.mmType = '(  )'
      this.mType = '(  )'
      this.checkLangguuage = false
    }
  }

  generateImage() {
    htmlToImage.toJpeg(document.getElementById('card-graph'), { quality: 0.95 })
      .then(function (dataUrl) {
        var link = document.createElement('a');
        link.download = 'image-name.jpeg';
        link.href = dataUrl;
        link.click();
      });

  }
  getStationHydrology() {
    this.spinner.show();
    const data = {};
    this.subs.push(
      this.webServices.getHydrologyBy_ListAll(data).subscribe(
        (res) => {
          this.spinner.hide();
          // console.log('testtstttst', res);
          if (res.status == 1) {
            this.station_data = res.data;
            if (this.checkLangguuage === true) {
              this.stationName = res.data[0]["name_eng"];

            } else {
              this.stationName = res.data[0]["name_lao"];

            }
            this.numberWarning = parseFloat(res.data[0]["alarm"]);
            this.numberDanger = parseFloat(res.data[0]["alert"]);
            console.log(this.station_data);
            this.loadDefaultHydrology(this.station_data);
          } else {
            this.AlertNOhavedata();
          }
        },
        (err) => {
          console.log(err);
          this.spinner.hide();
          this.AlertNOhavedata();

        }
      )
    );
  }

  getHydrologyById(myData: any) {
    if (myData) {
      const newData = myData.split(",");
      this.stationName = newData[1];
      const typeStation = newData[4]
      console.log('stationName', newData);

      this.numberWarning = parseFloat(newData[2]);
      this.numberDanger = parseFloat(newData[3]);
      this.spinner.show();
      const data = {
        station_id: newData[0],
        type: typeStation
      };
      console.log('TEST DTAT', data);

      this.loadRainGauge1Hours(newData[0])
      this.webServices.hydrologyForgraph(data).subscribe((res) => {
        console.log("data hydrology coming", res);
        this.spinner.hide();
        if (res.data.length > 0) {
          this.hydrologyList = res.data;
          console.log('TEST NO', res.data);

          this.lastUpdate = res.data[0]["time"];
          this.lastWaterLevel = parseFloat(res.data[0]["water_level"]);
          this.lastWaterTemperature = parseFloat(
            res.data[0]["water_temperature"]
          ).toFixed(0);

          let sumRain = 0
          for (let index = 0; index < 6; index++) {
            const element = parseInt(res.data[index]["rain_fall"]);

            sumRain = sumRain + element
          }
          this.lastRainFall = sumRain.toString();
          // this.lastRainFall = parseFloat(res.data[0]["rain_fall"]).toFixed(1);
          console.log('okokokok', this.lastRainFall);

          this.manageDataChart(this.hydrologyList);
        } else {
          this.AlertNOhavedata();
          this.spinner.hide();

        }
      });
    }
  }

  loadDefaultHydrology(station: Array<any>) {
    const stationId = station[0]["id"];
    const data = {
      station_id: stationId,
    };
    this.spinner.show()
    this.loadRainGauge1Hours(stationId)
    this.webServices.hydrologyForgraph(data).subscribe(
      (res) => {
        this.spinner.hide()

        // console.log("load defaul data coming", res);
        this.hydrologyList = res.data;
        // this.lastUpdate = res.data[0]["time"];
        this.lastUpdate = res.data[0]["time"];

        this.lastWaterLevel = parseFloat(res.data[0]["water_level"]);
        this.lastWaterTemperature = parseFloat(
          res.data[0]["water_temperature"]
        ).toFixed(0);
        this.lastRainFall = parseFloat(res.data[0]["rain_fall"]).toFixed(1);
        console.log("OKokok", this.lastRainFall);

        this.manageDataChart(this.hydrologyList);
      },
      (err) => {
        console.log(err);
        this.spinner.hide();

      }
    );
  }
  fillterAraay(data: Array<any>) {
    const newArray = data.filter(function (item) {
      return newArray;
    });
  }
  loadRainGauge1Hours(id: number) {
    const data = {
      station_id: id
    }
    console.log('tests id', data);

    this.webServices.rainGaugeForgraph(data).subscribe
      (res => {
        console.log('dddddd', res);
        if (res.data == []) {
          this.AlertNOhavedata()
          // console.log('if ok tetststs');
          // console.log('myisss');

        } else {
          // this.lastRainFall = res.data
          this.lastRainFall = parseFloat(res.data["rain"]).toFixed(1);
          console.log('myisss', this.lastRainFall);
          this.fillterAraay(res.data["rain"])
          console.log('myisss', this.fillterAraay);


        }
      }), err => {
        console.log(err);

      }
  }
  manageDataChart(data: Array<any>) {
    this.dataChart = [];
    this.dataTimeChart = [];
    this.lineWarning = [];
    this.lineDanger = [];
    this.rain_data = []
    console.log(this.numberWarning, this.numberDanger);

    for (let index = 0; index < data.length; index++) {
      const myIndex = data.length - (index + 1);
      const element = data[myIndex];
      const water =
        element["water_level"] === "" || element["water_level"] === "E6"
          ? 0
          : element["water_level"];

      const rain_fall =
        element["rain_fall"] === "" || element["rain_fall"] === "E6"
          ? 0
          : element["rain_fall"];
      const water_level = parseFloat(water);
      const myrain_fall = parseFloat(rain_fall);
      const dateTime = element["time"];
      let check = moment(dateTime, "YYYY/MM/DD H:mm");
      let hours = check.format("H");
      let minute = check.format("mm");
      if (hours.length <= 1) hours = "0" + hours;
      if (minute.length <= 1) minute = "0" + minute;
      const myTime = hours + ":" + minute;
      this.dataChart.push(water_level);
      this.dataTimeChart.push(myTime);
      this.rain_data.push(myrain_fall)
      this.lineWarning.push(this.numberWarning);
      this.lineDanger.push(this.numberDanger);
    }

    this.myDataChart2();
  }

  myDataChart2() {
    console.log('myisss', this.rain_data);

    const option = {
      chart: {
        zoomType: 'xy',
        style: {
          fontFamily: 'boonhom'
        }
      },
      title: {
        text: this.graph_Name,
        style: {
          fontFamily: 'boonhom',
          color: "#70cce1",

        }


      },
      xAxis: [{
        categories: this.dataTimeChart,
        crosshair: true
      }],
      yAxis: [{ // Primary yAxis
        labels: {
          format: '{value} ' + this.mType,
          style: {
            color: Highcharts.getOptions().colors[1]
          }
        },
        title: {
          text: this.waterLevel,
          style: {
            color: Highcharts.getOptions().colors[1]
          }
        }
      }, { // Secondary yAxis
        title: {
          text: this.rainFall,
          style: {
            color: Highcharts.getOptions().colors[0]
          }
        },
        labels: {
          format: '{value} ' + this.mmType,
          style: {
            color: Highcharts.getOptions().colors[0]
          }
        },
        opposite: true
      }],
      tooltip: {
        shared: true,
      },

      series: [
        {
          name: this.warnning + this.mType,
          type: 'line',
          data: this.lineWarning,
          color: "goldenrod",
          lineColor: "goldenrod",
          legend: {
            enabled: false,
          },
        },
        {
          name: this.dangerlevel + this.mType,
          type: 'line',
          data: this.lineDanger,
          color: "red",
          lineColor: "red",
        },
        {
          name: this.rainFall + this.mmType,
          type: 'column',
          yAxis: 1,
          data: this.rain_data,
          tooltip: {
            valueSuffix: +this.mmType
          }

        }, {
          name: this.waterLevel + this.mType,
          type: 'spline',
          data: this.dataChart,
          tooltip: {
            valueSuffix: this.mType
          }
        }],
      exporting: {
        buttons: {
          contextButton: {
            symbol: null,
            menuItems: null,
            text: 'Download',
            onclick: function () {
              this.exportChart({
                type: 'image/png'
              });
            }
          }
        }
      }
    };
    this.chartOptions = option;
  }


  AlertNOhavedata() {
    Swal.fire({
      icon: "warning",
      title: "!",
      text: "",
    }).then((result) => {
      this.getStationHydrology();
    });
  }
}
