import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import * as moment from "moment";
import { HydrologyService } from "../../services/hydrology.service";
import { StationService } from "../../services/station.service";

// import {
//   WindRose,
//   WindDescription,
//   Service,
// } from "../../services/chartservices";
// import { SynoticService } from "../../services/synotic.service";
import { NgxSpinnerService } from "ngx-spinner";
@Component({
  selector: "app-atustation",
  templateUrl: "./atustation.component.html",
  styleUrls: ["./atustation.component.scss"],
  // providers: [Service],
})
export class AtustationComponent implements OnInit {
  public isCollapsed: boolean = false;
  public setofpart: string = "";
  public limit: number = 12;
  public page: number = 1;
  public keyword: string = "";

  //data

  public hydrology_data: any;
  constructor(
    private router: Router,
    private hydrologyService: HydrologyService,
    private spinner: NgxSpinnerService
  ) {}
  public laotime: any = moment.locale("lo");
  public dateNow: any = moment().format("llll");

  ngOnInit(): void {
    this.loadingPage();
  }
  loadingPage() {
    this.getStationListByser();
  }
  getStationListByser() {
    const data = {
      limit: this.limit,
      page: this.page,
      keyword: this.keyword,
    };
    console.log("getHydrologyList", data);
    this.spinner.show();
    this.hydrologyService.getHydrologyList(data).subscribe((res) => {
      this.hydrology_data = res.data;
      console.log("getHydrologyList", this.hydrology_data);
      this.spinner.hide();
    }),
      (err) => {
        console.log(err);
      };
  }

  getStationListBypart(part: string) {
    const data = {
      part: part,
      limit: (this.limit = 10),
      page: this.page,
      keyword: this.keyword,
    };
    try {
      this.hydrologyService.getHydrologyListPart(data).subscribe((res) => {
        this.hydrology_data = res.data;
      }),
        (err) => {
          console.log(err);
        };
    } catch (error) {
      console.log(error);
    }
    console.log("error loading data");
  }

  onsearching(event: KeyboardEvent) {
    this.keyword = (event.target as HTMLInputElement).value;
    console.log("key seacrh", this.keyword);
    this.getStationListByser();
  }

  stationlist(count: string, station_id: number) {
    console.log("tetsttsttsts");

    this.router.navigate(["/climate-detail"], {
      queryParams: { coutdate: count, station_id: station_id },
    });
  }
  checkMyDate(data: any) {
    let myNum = moment(data, "YYYYMMDD, h:mm:ss a").fromNow();
    let countDate = parseInt(myNum.split(" ")[0]);

    let myDay = myNum.split(" ")[1];

    if (myDay == "") {
      let allDay = countDate * 365;
      // console.log("Year", allDay);
      return allDay;
    } else if (myDay == "") {
      let allDay = countDate * 30;
      // console.log("month", allDay);
      return allDay;
    } else if (myDay == "") {
      // console.log("day", countDate);
      return countDate;
    } else if (myDay == "") {
      // console.log("hour", countDate);
      return 1;
    } else {
      return 1;
    }
  }

  setpartlao(part: number) {
    if (part == 1) {
      this.setofpart = "";
    } else if (part == 2) {
      this.setofpart = "";
    } else if (part == 3) {
      this.setofpart = "";
    } else if (part == 4) {
      this.setofpart = "";
    }
  }
}
