  getidandType(id: number) {
    this.spinner.show();

    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
      id: id,
    };

    this.webServices.getList_allStationByid(data).subscribe((res) => {
      this.spinner.hide();

      const datacommit = res.data[0];
      this.stationname = datacommit.name_lao;
      this.getstationBYId(datacommit.id, datacommit.type);
    }),
      (err) => {
        console.log(err);
      };
  }

  getRainGaugByid(even) { }

  getstationBYId(station_id: number, type_station: string) {
    this.spinner.show();

    const data = {
      page: this.page,
      limit: (this.limit = 1),
      keyword: this.keyword,
      station_id: station_id,
    };

    if (type_station == "Hydrology") {
      try {
        this.webServices.getHydrologyBy_stationId(data).subscribe((res) => {
          this.spinner.hide();

          if (res.status == 1) {
            if (res.data.Data) {
              this.station_dataByid = res.data.Data[0];
            } else {
              this.AlertNOhavedata();
            }
          } else {
            this.AlertNOhavedata();
          }
        }),
          (err) => {
            console.log(err);
          };
      } catch (error) {
        console.log(error);
      }
    } else if (type_station == "CLIMATE") {
      console.log("name");

      try {
        this.webServices.getSynoticBy_stationId(data).subscribe((res) => {
          this.spinner.hide();

          if (res.status == 1) {
            if (res.data.Data) {
              this.station_dataByid = res.data.Data[0];
            } else {
              this.AlertNOhavedata();
            }
          } else {
            this.AlertNOhavedata();
          }
        }),
          (err) => {
            console.log(err);
          };
      } catch (error) {
        console.log(error);
      }
    } else if (type_station == "Rain Gauge") {
      this.webServices.getRainGaugeListDataBYid(data).subscribe((res) => {
        // console.log("Rain Gauge", res);

        this.spinner.hide();

        if (res.status == 1) {
          if (res.data.Data) {
            this.station_dataByid = res.data.Data[0];
          } else {
            this.AlertNOhavedata();
          }
        } else {
          this.AlertNOhavedata();
        }
      }),
        (err) => {
          console.log(err);
        };
    } else if (type_station == "SYNOPTIC") {
      this.webServices.getSynoticBy_stationId(data).subscribe((res) => {
        this.spinner.hide();

        if (res.status == 1) {
          if (res.data.Data) {
            this.station_dataByid = res.data.Data[0];
          } else {
            this.AlertNOhavedata();
          }
        } else {
          this.AlertNOhavedata();
        }
      }),
        (err) => {
          console.log(err);
        };
    } else {
      this.AlertNOhavedata22(type_station);
    }
  }

  AlertNOhavedata22(data: string) {
    Swal.fire({
      icon: "error",
      title: "Oops...",
      text: data + "",
      // footer: '<a href="">Why do I have this issue?</a>'
    });
  }

  AlertNOhavedata() {
    Swal.fire({
      icon: "error",
      title: "Oops...",
      text: " !",
      // footer: '<a href="">Why do I have this issue?</a>'
    });
  }
