import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { StationComponent } from '../station/station.component';
import Swal from 'sweetalert2';
import { AuthService } from '../../../services/auth.service';

@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.scss']
})
export class AddUserComponent implements OnInit {

  public statuscheck: string
  // public checkupdate: string
  public Setlanguse: string = localStorage.getItem('language')

  public username: string;
  public password: string;
  public isActive: boolean;
  public passwordNumberConfirm: string
  public setupRole: string = "0";

  public userid: string
  constructor(

    private route: ActivatedRoute,

    private authService: AuthService,
    private router: Router
  ) {

  }

  ngOnInit(): void {
    this.isActive = true
    this.load()
  }
  load() {
    this.route
      .queryParams
      .subscribe(params => {
        this.statuscheck = params.statuscheck
        if (this.statuscheck === "updateuser") {
          this.getuserBYId(params.updateuserId)
        }
        this.userid = params.updateuserId
        console.log('check update key', this.statuscheck);
      });
  }
  checkFrom() {
    if (!this.username) {
      Swal.fire('')
    }
    else if (!this.password) {
      Swal.fire('')

    } else if (this.passwordNumberConfirm !== this.password) {
      Swal.fire('')
    }
    else if (this.setupRole === "0") {
      Swal.fire('')
    }
    else {
      this.isCreateUser()
    }
  }

  checkFromUpdate() {
    if (!this.username) {
      Swal.fire('')
    }
    else if (!this.password) {
      Swal.fire('')

    } else if (!this.setupRole) {
      Swal.fire('')

    }
    else {
      this.isUpdateUsers()
    }
  }

  isCreateUser() {
    const data = new AddUser
    data.username = this.username
    data.password = this.password
    data.role = this.setupRole
    data.isActive = this.isActive

    console.log('Create user data before sent to data', data);
    this.authService.createUser(data).subscribe
      (res => {
        console.log('testttss', res);
        if (res.status == '1') {
          if (this.Setlanguse == 'lao') {
            this.alertsuccess()
          } else {
            this.alertsucEng()
          }
          this.alertsuccess()
          this.router.navigate(['/usermanager'])

        } else {
          this.alertfalse()
        }
      })
  }


  isUpdateUsers() {
    const data = new AddUser
    data.userid = this.userid
    data.username = this.username
    data.password = this.password
    data.isActive = this.isActive
    data.role = this.setupRole

    console.log('update user data before sent to data', data);

    this.authService.updateUser(data).subscribe
      (res => {
        if (res.status == '1') {
          if (this.Setlanguse == 'lao') {
            this.alertsuccessUpdate()

          } else {
            this.alertsuccessUpdateng()
          }
          this.router.navigate(['/usermanager'])

        } else {
          this.alertfalse()
        }

      })
  }


  alertsuccess() {
    Swal.fire(
      '',
      '!',
      'success'
    )
  }
  alertsucEng() {
    Swal.fire(
      'Successfully',
      'Click To close!',
      'success'
    )
  }
  alertsuccessUpdate() {
    Swal.fire(
      '',
      '!',
      'success'
    )
  }
  alertsuccessUpdateng() {
    Swal.fire(
      'Successfully',
      'Click To close!',
      'success'
    )
  }


  alertfalse() {
    Swal.fire({
      icon: 'error',
      title: 'Oops...',
      text: 'Something went wrong!',
      // footer: '<a href="">Why do I have this issue?</a>'
    })
  }

  getuserBYId(id: string) {
    const data = {
      userid: id
    }
    console.log('my id', data);

    this.authService.getUserById(data).subscribe
      (res => {
        console.log(res);
        this.username = res.data[0]?.username
        this.password = res.data[0]?.password;
        this.setupRole = res.data[0]?.role;
      })
  }
}

class AddUser {
  userid: string
  username: string;
  password: string;
  role: string;
  passwordNumberConfirm: string
  isActive: boolean;
}

