import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { APIService } from "./api.service";
@Injectable({
  providedIn: "root",
})
export class HydrologyService extends APIService {
  public url: string = this.getBaseUrl();
  public token = "";

  getsynoticList(param: any): Observable<any> {
    const header = this.headerBase("synopticListPage");
    return this.http.post(this.url + "synoptic.api.php", param, {
      headers: header,
    });
  }
  getStationBylatLng(param: any): Observable<any> {
    const header = this.headerBase("synopticListByStationLatLng");
    return this.http.post(this.url + "synoptic.api.php", param, {
      headers: header,
    });
  }
  synopticListByTime(param: any): Observable<any> {
    const header = this.headerBase("synopticListByTime");
    return this.http.post(this.url + "synoptic.api.php", param, {
      headers: header,
    });
  }

  getsynoticListChilldrenStation(param: any): Observable<any> {
    const header = this.headerBase("synopticChilldrenOfStation");
    return this.http.post(this.url + "synoptic.api.php", param, {
      headers: header,
    });
  }

  getsynoticListChilldrenStationByPart(param: any): Observable<any> {
    const header = this.headerBase("synopticChilldrenOfStationByPart");
    return this.http.post(this.url + "synoptic.api.php", param, {
      headers: header,
    });
  }

  getHydrologyList(param: any): Observable<any> {
    const header = this.headerBase("hydrologyChilldrenOfStation");
    return this.http.post(this.url + "hydrology.api.php", param, {
      headers: header,
    });
  }
  getHydrologyListByuser(param: any): Observable<any> {
    const header = this.headerBase("stationHydrologyListAllByUser");
    return this.http.post(this.url + "station.api.php", param, {
      headers: header,
    });
  }
  getHydrologyListById(param: any): Observable<any> {
    const header = this.headerBase("hydrologyListByStation");
    return this.http.post(this.url + "hydrology.api.php", param, {
      headers: header,
    });
  }

  getHydrologyListPart(param: any): Observable<any> {
    const header = this.headerBase("hydrologyChilldrenOfStationByPart");
    return this.http.post(this.url + "hydrology.api.php", param, {
      headers: header,
    });
  }
  getHydrologyForGraph(param: any): Observable<any> {
    const header = this.headerBase("hydrologyListForGraph");
    return this.http.post(this.url + "hydrology.api.php", param, {
      headers: header,
    });
  }

  getHydrologyListChilldrenOfstation(param: any): Observable<any> {
    const header = this.headerBase("stationHydrologyListAllByUser");
    return this.http.post(this.url + "hydrology.api.php", param, {
      headers: header,
    });
  }
  reportHydrology(param: any): Observable<any> {
    const header = this.headerBase("reportHydrologyChildrenStationAndFeture");
    return this.http.post(this.url + "report.api.php", param, {
      headers: header,
    });
  }
  reportHydrologyCSV(param: any): Observable<any> {
    const header = this.headerBase(
      "reportHydrologyByAnyStationAndTimeAndFeture"
    );
    return this.http.post(this.url + "report.api.php", param, {
      headers: header,
    });
  }
}
