import { Injectable } from "@angular/core";

export enum E_Direction {
  N = "N",
  S = "S",
  E = "E",
  W = "W",
  NNE = "NNE",
  NE = "NE",
  ENE = "ENE",

  NNW = "NNW",
  NW = "NW",
  WNW = "WNW",

  SE = "SE",
  ESE = "ESE",
  SSE = "SSE",

  SW = "SW",
  WSW = "WSW",
  SSW = "SSW",
}
export interface WindValue {
  arg: E_Direction;
  val1: number;
  val2: number;
  val3: number;
  val4: number;
  val5: number;
  val6: number;
  // val7: number;
  // val8: number;
}
// let NNE: any = {
//     arg: "NNE",
// }
// NNE['val1'] = 288

// const wind_directions: number = 22

export interface WindRose {
  period: string;
  values: WindValue[];
}

export class WindDescription {
  valueField: string;
  name: string;
}
export class WindSource {
  avg_wind_direction: number = 0;
  avg_wind_speed: number = 0;
  time: string;
}
@Injectable()
export class Service {
  ws: Array<WindDescription> = [];
  windRoseData: WindRose[] = [
    {
      period: "",
      // values: []
      values: [
        { arg: E_Direction.N, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.NNE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.NE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.ENE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.E, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.ESE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.SE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.SSE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.S, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.SSW, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.SW, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.WSW, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.W, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.WNW, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.NW, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.NNW, val1: 0, val2: 0, val3: 0 } as WindValue,
      ],
    },
  ];
  getDirection(v: number = 0) {
    const x = 22.5;
    let i = 1;
    // console.log(v, x);

    if (v >= 0 && v < x * i++) {
      return E_Direction.N;
    }
    if (x * i++ > v) {
      return E_Direction.NNE;
    }
    if (x * i++ > v) {
      return E_Direction.NE;
    }
    if (x * i++ > v) {
      return E_Direction.ENE;
    }
    if (x * i++ > v) {
      return E_Direction.E;
    }
    if (x * i++ > v) {
      return E_Direction.ESE;
    }
    if (x * i++ > v) {
      return E_Direction.SE;
    }
    if (x * i++ > v) {
      return E_Direction.SSE;
    }
    if (x * i++ > v) {
      return E_Direction.S;
    }
    if (x * i++ > v) {
      return E_Direction.SSW;
    }
    if (x * i++ > v) {
      return E_Direction.SW;
    }
    if (x * i++ > v) {
      return E_Direction.WSW;
    }
    if (x * i++ > v) {
      return E_Direction.W;
    }
    if (x * i++ > v) {
      return E_Direction.WNW;
    }
    if (x * i++ > v) {
      return E_Direction.NW;
    }
    if (x * i++ > v) {
      return E_Direction.NNW;
    }
  }
  getWindSources(data: Array<WindSource> = []) {
    // console.log("data comming ok on service chart", data);

    for (let i = 0; i < data.length; i++) {
      const e = data[i];
      if (!this.ws.filter((v) => v.name === e.time).length)
        this.ws.push({ valueField: "val" + (i + 1), name: e.time });
    }
    // console.log(this.ws.length, this.ws);
    return this.ws;
  }
  getWindRoseData(data: Array<WindSource> = [], period: string = "") {
    //data.sort((a,b)=>{return a.})
    try {
      //   console.log("TEST", data);
      this.windRoseData[0].values = [
        { arg: E_Direction.N, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.NNE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.NE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.ENE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.E, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.ESE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.SE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.SSE, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.S, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.SSW, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.SW, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.WSW, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.W, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.WNW, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.NW, val1: 0, val2: 0, val3: 0 } as WindValue,
        { arg: E_Direction.NNW, val1: 0, val2: 0, val3: 0 } as WindValue,
      ];
      this.windRoseData[0].period = period;

      //   console.log("TEST ", this.windRoseData);
      for (let index = 0; index < data.length; index++) {
        const e = data[index];
        //
        const d = this.getDirection(
          Number.parseFloat(e.avg_wind_direction + "")
        );
        const wspeed = Number.parseFloat(e.avg_wind_speed + "");
        const val = "val" + (index + 1);
        const v = {
          arg: d,
        };
        v[val] = wspeed;
        const ix = this.windRoseData[0].values.findIndex((v) => v.arg === d);
        if (ix > -1) {
          //   console.log(e.avg_wind_direction, d);
          this.windRoseData[0].values[ix][val] = wspeed;
        }
        // this.windRoseData[0].values.push(v as WindValue);
      }
      // console.log('TEST ', JSON.stringify(this.windRoseData))
    } catch (error) {
      console.log("TEST", error);
    }

    return this.windRoseData;
  }
}
