import { Component, OnInit } from "@angular/core";
import { Router, NavigationEnd } from "@angular/router";

import { IconSetService } from "@coreui/icons-angular";
import { freeSet } from "@coreui/icons";
import { TranslateService } from "@ngx-translate/core";
import { ProgressDowloadfileService } from "../services/progress-dowloadfile.service";

@Component({
  // tslint:disable-next-line
  selector: "body",
  template: `<ngx-spinner
      bdColor="rgba(51,51,51,0.8)"
      size="medium"
      color="#fff"
      type="ball-scale-multiple"
    >
      <p style="font-size: 20px; color: rgba(241, 241, 241, 0.521)">
        ...
        <strong *ngIf="progress.progress > 0"
          >{{ progress.progress }} MB</strong
        >
      </p>
    </ngx-spinner>
    <router-outlet></router-outlet>`,
  providers: [IconSetService],
})
export class AppComponent implements OnInit {
  constructor(
    private router: Router,
    public iconSet: IconSetService,
    public translate: TranslateService,
    public progress: ProgressDowloadfileService
  ) {
    // iconSet singleton
    iconSet.icons = { ...freeSet };
  }

  ngOnInit() {
    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
        return;
      }
      window.scrollTo(0, 0);
    });
    this.initializeApp();
  }

  initializeApp() {
    this.translate.setDefaultLang("eng");
    if (localStorage.getItem("language")) {
      this.translate.use(localStorage.getItem("language"));
    }
  }
}
