import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import * as moment from "moment";
import { StationService } from "../../services/station.service";
import Swal from "sweetalert2";
import { AppServiceCsv } from "../../services/mycsv";
import { XlsxService } from "../../services/xlsx.service";
import { ReportfilecsvService } from "../../services/reportfilecsv.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ProgressDowloadfileService } from "../../services/progress-dowloadfile.service";

@Component({
  selector: "app-report",
  templateUrl: "./report.component.html",
  styleUrls: ["./report.component.scss"],
})
export class ReportComponent implements OnInit {
  public isCollapsed: boolean = false;
  public page: number = 1;
  public limit: number = 100;
  public keyword: string = "";
  public station_data: any;

  public Myselected: boolean;
  public stationListAllOne: Array<any>;
  public stationListAllTwo: Array<any>;

  public featrueListOne = [
    "avg_wind_speed",
    "avg_wind_direction",
    "wind_gust",
    "wind_status",
    "air_temperature",
    "relative_humidity",
    "air_pressure",
  ];
  public featrueListtwo = [
    "dewpoint",
    "metpak_status",
    "net_radiation",
    "rain_tips",
    "rain_fall",
    "ext_batt",
    "moisture",
    "modern_signal",
  ];
  public synotic_data_report: any;
  //data
  public dateNow: any = moment().format("llll");
  public datalist: any;
  public station_id: string;
  public array_id: Array<number> = [];
  public feature: Array<string> = [];

  public userId: number;
  public startTime: string;
  public endTime: string;
  public checkAdmin: boolean = false;
  public checklanguae: boolean = false;

  constructor(
    private router: Router,
    public stationservice: StationService,
    private appService: AppServiceCsv,
    private exportss: XlsxService,
    public reportservice: ReportfilecsvService,
    public xlsx: XlsxService,
    private spinner: NgxSpinnerService,
    private progress: ProgressDowloadfileService
  ) { }

  ngOnInit(): void {
    this.getListstationAll();
    this.checkLanguageF()
  }
  checkLanguageF() {
    if (localStorage.getItem('language') === 'eng') {
      this.checklanguae = true
      console.log(this.checklanguae);

    } else {
      this.checklanguae = false
      console.log(this.checklanguae);

    }
  }

  getListstationAll() {
    const userids = JSON.parse(localStorage.getItem("userData"));
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
      userid: userids.id,
    };
    console.log("loading station", data);

    this.stationservice.getStationSynopticAllByUser(data).subscribe((res) => {
      console.log(res);

      if (res.status == 1) {
        this.station_data = res.data;
        console.log('my station', this.station_data);
        let result = res.data;
        let numAll: number = parseInt(result.length);
        let myNum: number = Math.ceil(numAll / 2);
        let dataOne = result.slice(0, myNum);
        let dataTwo = result.slice(myNum);
        this.stationListAllOne = [];
        this.stationListAllOne = dataOne;
        this.stationListAllTwo = [];
        this.stationListAllTwo = dataTwo;
      }
    });
  }

  changeGender(e) {
    let idchange = e.target.value;
    let myid = parseInt(idchange);
    if (e.target.checked) {
      this.array_id.push(myid);
    } else {
      for (let index = 0; index < this.array_id.length; index++) {
        const element = this.array_id[index];
        if (myid == element) {
          this.array_id.splice(index, 1);
        }
      }
    }
    console.log("ssssssssssssss", this.array_id);
  }
  clickLselectAll(e) {
    let checkall = e.target.checked;

    let checkboox = document.getElementsByTagName("input");
    if (checkall) {
      for (let i = 0; i < checkboox.length; i++) {
        if (checkboox[i].type == "checkbox") {
          checkboox[i].checked = true;
        }
      }
      let stationid = this.station_data;
      this.array_id = [];
      for (let index = 0; index < stationid.length; index++) {
        const element = stationid[index];
        this.array_id.push(parseInt(element.id));
      }
      // console.log("myId ", this.array_id);
    } else {
      for (let index = 0; index < checkboox.length; index++) {
        if (checkboox[index].type == "checkbox") {
          checkboox[index].checked = false;
        }
      }
      this.array_id = [];
      // console.log("myId ", this.array_id);
    }
  }

  clickLselectAll2(e) {
    let checkall = e.target.checked;

    let checkboox: any = document.getElementsByName("station");

    if (checkall) {
      for (let i = 0; i < checkboox.length; i++) {
        checkboox[i].checked = true;
      }

      let idStation = this.station_data;
      console.log("idStation", idStation);
      for (let index = 0; index < idStation.length; index++) {
        const element = idStation[index];
        console.log("test", element.id);
        this.array_id.push(element.id);
      }
    } else {
      for (let index = 0; index < checkboox.length; index++) {
        checkboox[index].checked = false;
      }
      this.array_id = [];
    }
  }

  getFeature(e) {
    let myid = e.target.value;
    if (e.target.checked) {
      this.feature.push(myid);
    } else {
      for (let index = 0; index < this.feature.length; index++) {
        const element = this.feature[index];
        if (myid == element) {
          this.feature.splice(index, 1);
        }
      }
    }
  }

  clearFeature() {
    this.feature = [];
    let checkboox: any = document.getElementsByName("featur");

    for (let i = 0; i < checkboox.length; i++) {
      if (checkboox[i].type == "checkbox") {
        checkboox[i].checked = false;
      }
    }
  }
  getFeatureAll(e) {
    let checkall = e.target.checked;

    let checkboox: any = document.getElementsByName("featur");

    if (checkall) {
      for (let i = 0; i < checkboox.length; i++) {
        checkboox[i].checked = true;
      }

      let myFeature = this.feature;

      this.feature = [
        "avg_wind_speed",
        "avg_wind_direction",
        "wind_gust",
        "wind_status",
        "air_temperature",
        "relative_humidity",
        "air_pressure",
        "dewpoint",
        "metpak_status",
        "net_radiation",
        "rain_tips",
        "rain_fall",
        "ext_batt",
        "moisture",
        "modern_signal",
      ];
    } else {
      for (let index = 0; index < checkboox.length; index++) {
        checkboox[index].checked = false;
      }
      this.feature = [];
    }
  }
  // changeStation(ev) {
  //   // console.log(ev);

  //   let checkboxs = document.getElementsByName("station");
  //   for (let index = 0; index < checkboxs.length; index++) {
  //     const element = checkboxs.item(index) as HTMLInputElement;
  //     if ((ev.target as Element) === (element as Element)) {
  //       element.checked = true;
  //       this.stationId = ev.target.value;
  //     } else {
  //       element.checked = false;
  //     }
  //   }

  //   console.log("my Station id", this.stationId);

  //   if (ev.target.checked == true) {
  //   }
  // }

  checkfromReportExcel() {
    if (this.array_id.length == 0) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (!this.startTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (!this.endTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (this.feature.length == 0) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else {
      this.exportToexcel();
    }
  }

  checkfromReportCSV() {
    if (this.array_id.length == 0) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (this.feature.length == 0) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (!this.startTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (!this.endTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else {
      this.exportTocsv();
    }
  }
  exportToexcel() {
    const id = this.array_id;
    const data = {
      station_id: id.map(Number).toString(),
      startTime: this.startTime + " 00:00:00",
      endTime: this.endTime + " 23:59:59",
      myColumn: this.feature,
    };
    this.spinner.show();
    console.log("Send data to server", data);
    try {
      this.reportservice
        .reportSynopticChildrenStation(data)
        .subscribe((res) => {
          console.log("2222222222222222222222222222222", res);
          if (res?.body?.status == 1) {
            this.progress.progress = 0;
            console.log("data cpmmogmg", res);
            this.exportss.exportExcelBysheet(res.body.data.value);
            this.spinner.hide();
          } else if (res?.body?.status == 0) {
            this.router.navigate(["/report"]);
            this.errorLoading();
            this.spinner.hide();
          } else {
            this.progress.updateStatus(res.loaded);
          }
        });
    } catch (error) {
      console.log(error);
      this.errorLoading();
    }
  }
  exportTocsv() {
    const id = this.array_id;
    const data = {
      station_id: id.map(Number).toString(),
      startTime: this.startTime + " 00:00:00",
      endTime: this.endTime + " 23:59:59",
      myColumn: this.feature,
    };
    console.log("Send data to server", data);
    this.spinner.show();
    const that = this;
    this.reportservice
      .getsynoticListByAnyStationAndTime(data)
      .subscribe((res) => {
        that.spinner.hide();
        if (res.status == 1) {
          that.synotic_data_report = res.data;
          that.xlsx.downloadFileCSV(res.data.value);
          that.spinner.hide();
        } else {
          that.spinner.hide();

          that.router.navigate(["/report"]);
          that.spinner.hide();
          that.errorLoading();
        }
      });
    that.spinner.hide();
  }

  errorLoading() {
    Swal.fire({
      icon: "warning",
      title: "!",
      text: " ",
      showConfirmButton: true,
    });
  }
}

export class Station {
  altitude: string;
  id: number;
  laoName: string;
  latitude: string;
  longitude: string;
  name_eng: string;
  part: string;
  station_model: string;
  station_number: string;
  station_phonenumber: string;
  type: string;
}
