import { Component, OnInit } from "@angular/core";
import { StationService } from "../../services/station.service";
import { StationS } from "../views/station/station.component";
import jsPDF from "jspdf";
import "jspdf-autotable";
import html2canvas from "html2canvas";
import { Router } from "@angular/router";

@Component({
  selector: "app-export-station-to-pdf",
  templateUrl: "./export-station-to-pdf.component.html",
  styleUrls: ["./export-station-to-pdf.component.scss"],
})
export class ExportStationToPDFComponent implements OnInit {
  public page: number = 1;
  public limit: number = 10;
  public keyword: string = "";

  public station_resport: Array<StationS>;

  constructor(public stationservice: StationService, private router: Router) { }

  ngOnInit(): void {
    this.getListallForExportPDF();
  }

  getListallForExportPDF() {
    const data = {
      page: "",
      limit: "",
      keyword: this.keyword,
    };
    this.stationservice.getStationAll(data).subscribe((res) => {
      console.log(res);
      if (res.status == 1) {
        this.station_resport = res.data;
        console.log("teststtttsts", this.station_resport);
        // this.getListallForExportPDFs();
      } else {
        console.log("no have data");
      }
    });
  }

  exporttoPDF() {
    let DATA = document.getElementById("htmlData");
    html2canvas(DATA).then((canvas) => {
      let fileWidth = 208;
      let fileHeight = (canvas.height * fileWidth) / canvas.width;

      const FILEURI = canvas.toDataURL("image/png");
      let PDF = new jsPDF("p", "mm", "a4");
      let position = 0;
      PDF.addImage(FILEURI, "PNG", 0, position, fileWidth, fileHeight);
      PDF.save("station_list.pdf");
    });
  }

  goback() {
    this.router.navigate(["/stationmanage"]);
  }
}
