import { Component, OnInit } from "@angular/core";

import { ActivatedRoute, Router } from "@angular/router";
import Swal from "sweetalert2";
import * as Leaflet from "leaflet";

import { NgxSpinnerService } from "ngx-spinner";
import { StationService } from "../../../services/station.service";

@Component({
  selector: "app-station-add",
  templateUrl: "./station-add.component.html",
  styleUrls: ["./station-add.component.scss"],
})
export class StationAddComponent implements OnInit {
  public statuscheck: string;

  //data
  public map: Leaflet.Map;
  //data
  public branchowner: string;
  // public latitude: any;
  // public longitude: any;
  // public alt: string;
  // public naem: string;
  // public station: string;
  // public id: string; //slecter

  public station_data: StationModel;

  //data
  public id: number;
  public laoName: string;
  public engName: string;
  public altitude: string;
  public latitude: number;
  public longitude: number;
  public station_number: string;
  public station_phonenumber: string;
  public station_model: string;
  public part: string;
  public type: string;

  public selectshowHydorogy: boolean = false;
  public alert: string;
  public alarm: string;
  public critical: string;
  constructor(
    private router: Router,
    private Spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    private stationservice: StationService
  ) { }

  ngOnInit(): void {
    this.station_data = new StationModel();
    this.type = "0";
    this.part = "0";
    this.loading();
  }

  ngOnDestroy() {
    this.map.remove();
  }

  loading() {
    this.route.queryParams.subscribe((params) => {
      this.statuscheck = params.statuscheck;
      if (params.statuscheck == "updatestation") {
        this.getstationbyId(params.stationId);
      } else {
        this.leafletMap();
      }
      this.id = params.stationId;
      console.log("check update key", this.statuscheck);
    });
    // this.getstationbyId()
  }

  onlectHydrolegy(event) {
    console.log("eventssssssss", event);

    if (event == "Hydrology") {
      this.selectshowHydorogy = true;
    } else {
      this.selectshowHydorogy = false;

      false;
    }
  }
  async leafletMap() {
    // this.Spinner.show();
    navigator.geolocation.getCurrentPosition((resp) => {
      console.log(resp);
      if (this.statuscheck !== "updatestation") {
        this.latitude = resp.coords.latitude;
        this.longitude = resp.coords.longitude;
      }

      const localtion = {
        latitude: resp.coords.latitude,
        longitude: resp.coords.longitude,
        name: "",
      };

      console.log(localtion);

      this.map = Leaflet.map("mapId").setView(
        [this.latitude, this.longitude],
        6
      );

      Leaflet.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        attribution: "edupala.com  Angular LeafLet",
      }).addTo(this.map);

      const customMarkerIcon = Leaflet.icon({
        iconUrl: "assets/img/custom-marker-icon.png",
        iconSize: [24, 24],
        iconAnchor: [6, 23],
        popupAnchor: [0, -20],
      });
      const pop = "<p>  </p>";
      let that = this;

      Leaflet.marker([this.latitude, this.longitude], {
        icon: customMarkerIcon,
        draggable: true,
      })
        .addTo(this.map)
        .bindPopup(pop)
        .on("dragend", function (e) {
          var marker = e.target;
          var position = marker.getLatLng();
          that.latitude = position.lat;
          that.longitude = position.lng;
          const localtion = {
            latitude: position.lat,
            longitude: position.lng,
            name: "",
          };
          console.log(that.latitude);
          console.log(that.longitude);
          // that.save(localtion)
          that.map.panTo(new Leaflet.LatLng(position.lat, position.lng));
        });
      that.Spinner.hide();
    }),
      (error) => {
        console.log("Error getting location", error);
      };
  }
  getstationbyId(id: number) {
    const data = {
      id: id,
    };
    this.stationservice.getById(data).subscribe((res) => {
      console.log("get success", res);
      this.station_model = res.data[0].station_model;
      this.station_phonenumber = res.data[0].station_phonenumber;
      this.part = res.data[0].part;
      this.laoName = res.data[0].name_lao;
      this.type = res.data[0].type;
      this.engName = res.data[0].name_eng;
      this.altitude = res.data[0].alt;
      this.longitude = res.data[0].lng;
      this.latitude = res.data[0].lat;
      this.station_number = res.data[0].station_number;
      this.alert = res.data[0].alert;
      this.alarm = res.data[0].alarm;
      this.critical = res.data[0].critical = "34";

      console.log(this.station_number);
      this.leafletMap();
    });
  }
  checkFrom() {
    if (!this.laoName) {
      Swal.fire("");
    } else if (!this.engName) {
      Swal.fire("");
    } else if (!this.station_number) {
      Swal.fire("");
    } else if (!this.latitude) {
      Swal.fire("longitude");
    } else if (!this.longitude) {
      Swal.fire("altitude");
    } else if (!this.altitude) {
      Swal.fire("alt");
    } else if (!this.station_model) {
      Swal.fire("");
    } else if (!this.station_phonenumber) {
      Swal.fire("");
    } else if (!this.alert) {
      Swal.fire(" alert");
    } else if (!this.alarm) {
      Swal.fire(" alarm");
    } else {
      this.addstation();
    }
  }

  checkFromUpdate() {
    if (!this.laoName) {
      Swal.fire("");
    } else if (!this.engName) {
      Swal.fire("");
    } else if (!this.station_number) {
      Swal.fire("latitude");
    } else if (!this.latitude) {
      Swal.fire("longitude");
    } else if (!this.longitude) {
      Swal.fire("altitude");
    } else if (!this.altitude) {
      Swal.fire("alt");
    } else if (!this.station_model) {
      Swal.fire("");
    } else if (!this.station_phonenumber) {
      Swal.fire("");
    } else if (!this.alert) {
      Swal.fire(" alert");
    } else if (!this.alarm) {
      Swal.fire(" alarm");
    } else {
      this.updatestation();
    }
  }

  addstation() {
    const data = {
      alt: this.altitude,
      lat: this.latitude,
      lng: this.longitude,
      name_eng: this.engName,
      name_lao: this.laoName,
      type: this.type,
      station_number: this.station_number,
      station_model: this.station_model,
      station_phonenumber: this.station_phonenumber,
      part: this.part,
      alert: this.alert,
      alarm: this.alarm,
      critical: (this.critical = "ww"),
    };
    console.log("data", data);
    this.stationservice.createStation(data).subscribe((res) => {
      console.log(res);
      if (res.status == 1) {
        this.clearTextInput();
        Swal.fire({
          // position: "top-end",
          icon: "success",
          title: "",
          showConfirmButton: false,
          timer: 1500,
        });
        this.router.navigate(["/stationmanage"]);
      } else {
        Swal.fire({
          // position: "top-end",
          icon: "error",
          title: "!",
          text: res.message,
          showConfirmButton: true,
        });
      }
    });
  }
  updatestation() {
    const data = {
      id: this.id,
      alt: this.altitude,
      lat: this.latitude,
      lng: this.longitude,
      name_eng: this.engName,
      name_lao: this.laoName,
      type: this.type,
      station_number: this.station_number,
      station_model: this.station_model,
      station_phonenumber: this.station_phonenumber,
      part: this.part,
      alert: this.alert,
      alarm: this.alarm,
      critical: 12,
    };
    console.log("data", data);
    this.stationservice.updateStation(data).subscribe((res) => {
      console.log("update", res);
      if (res.status == 1) {
        Swal.fire({
          icon: "success",
          title: "!",
          showConfirmButton: false,
          timer: 1500,

        });
        console.log('testtttttttttt');
        this.router.navigate(["/stationmanage"]);


      } else {
        Swal.fire({
          icon: "error",
          title: "!",
          text: res.message,
          showConfirmButton: true,
        });
      }
    });
    this.clearTextInput();
  }

  clearTextInput() {
    this.altitude = "";
    this.latitude = 0;
    this.longitude = 0;
    this.engName = "";
    this.laoName = "";
    this.type = "0";
    this.station_number = "";
    this.station_phonenumber = "";
    this.station_model = "";
    this.part = "0";
    this.alert = "";
    this.alarm = "";
    this.critical = "";
  }
}
export class StationModel {
  alt: number;
  id: number;
  lat: number;
  lng: number;
  name_eng: string;
  type: string;
  name_lao: string;
  station_number: string;
  station_phonenumber: string;
  station_model: string;
  alert: string;
  alarm: string;
  critical: string;
}
