import { Component, OnInit } from "@angular/core";
import * as moment from "moment";
import { Subscription } from "rxjs";
import { WaterserviceService } from "../../services/waterservice.service";

@Component({
  selector: "app-water-station",
  templateUrl: "./water-station.component.html",
  styleUrls: ["./water-station.component.scss"],
})
export class WaterStationComponent implements OnInit {
  public isCollapsed: boolean = false;
  private subscriptions = new Subscription();
  public waterList_Data: any;
  constructor(private water: WaterserviceService) { }

  ngOnInit(): void {
    this.get_waterStationListBy();
    console.log("testts loading");
  }
  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  get_waterStationListBy() {
    const local = JSON.parse(localStorage.getItem("userData"));
    const data = {
      userid: local.id,
    };
    console.log(data);
    try {
      this.water.getList_waterStation(data).subscribe((r) => {
        this.waterList_Data = r.data;
        console.log("water station list", this.waterList_Data);
      }),
        (err) => {
          console.log(err);
        };
    } catch (error) {
      console.log(error);
    }
  }
  checkMyDate(data: any) {
    let myNum = moment(data, "YYYYMMDD, h:mm:ss a").fromNow();
    let countDate = parseInt(myNum.split(" ")[0]);
    let myDay = myNum.split(" ")[1];

    if (myDay == "") {
      let allDay = countDate * 365;
      // console.log("Year", allDay);
      return allDay;
    } else if (myDay == "") {
      let allDay = countDate * 30;
      // console.log("month", allDay);
      return allDay;
    } else if (myDay == "") {
      // console.log("day", countDate);
      return countDate;
    } else if (myDay == "") {
      // console.log("hour", countDate);
      return 1;
    } else {
      return 1;
    }
  }
}
