import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { API_SERVERLOCAL } from "../environments/environment.prod";
@Injectable({
  providedIn: "root",
})
export class APIService {
  constructor(public http: HttpClient) { }
  protected getBaseUrl(): string {
    // return "http://192.168.2.203/forecast/forecasting/api/";
    // return "http://192.168.1.119/forecast/forecasting/api/";

    return "http://115.84.105.25:9393/api/";
    // return "http://115.84.105.25:9393/api/";
    // return "http://172.20.10.4/forecast/forecasting/api/";

    // return API_SERVERLOCAL;
    // return "http://laoapps.com:34588/";
  }

  protected headerBase(m: string = ""): any {
    const token = localStorage.getItem("Token");
    const headers = new HttpHeaders({ "Content-Type": "application/json" })
      // .set("Access-Control-Allow-Origin", "*")
      .set("Token", token + "")
      .set("M", m);
    return headers;
  }
}
