import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import * as moment from "moment";
import { HydrologyService } from "../../services/hydrology.service";
// import { Data, ServiceDataChart } from "../../services/chartservicesData";
// import * as CanvasJS from './canvasjs.min';
import * as CanvasJS from "../../assets/canvasjs-3.3.2/canvasjs.min";
import { SynoticService } from "../../services/synotic.service";
import { StationService } from "../../services/station.service";
// import { SynoticService } from "../../../services/synotic.service";

import Swal from "sweetalert2";
import { NgxSpinnerService } from "ngx-Spinner";
//import * as CanvasJS from 'canvasjs';
@Component({
  selector: "app-graph",
  templateUrl: "./graph.component.html",
  styleUrls: ["./graph.component.scss"],
  // providers: [ServiceDataChart]
})
export class GraphComponent implements OnInit {
  public userId: number;
  public date: string = moment().format("Do MMM");
  // public dataSource: Data[];
  public array_id: Array<number> = [];

  public myDataForGraph: Array<any> = [];
  public isCollapsed: boolean = false;
  public limit: number = 12;
  public page: number = 1;
  public keyword: string = "";
  public checkAdmin: boolean = false;
  //data
  public dataNewCompare: Array<any> = [];

  public dataNewCompareOne: Array<any>;
  public dataNewCompareTwo: Array<any>;

  public datalist: any;
  public stationListAllByUser: any;

  public stationId: number;
  public feature: Array<string> = [];
  public startTime: string;
  public endTime: string;

  public onClickShowGraph: boolean = false;
  public haveNOdataGraph: boolean = false;
  public statin_data: any
  constructor(
    private router: Router,
    private serviceGetstation: HydrologyService,
    private synotic: SynoticService,
    public stationservice: StationService,
    private Spinner: NgxSpinnerService
  ) {
    // this.dataSource = chartservice.getData();
  }
  public laotime: any = moment.locale("lo");
  public dateNow: any = moment().format("llll");

  ngOnInit(): void {
    const local = JSON.parse(localStorage.getItem("userData"));
    this.userId = local.id;
    // if (local.role === "admin") {
    //   this.checkAdmin = true;
    // } else {
    //   this.checkAdmin = false;
    // }
    // this.loadingPage();

    // this.getstationAllByUser();
  }

  clickLoad() {
    this.myDataForGraph = [];
    if (!this.stationId) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (this.feature.length === 0) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: " feature",
        showConfirmButton: true,
      });
    } else if (!this.startTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (!this.endTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else {
      this.getDataSynopticForGraph();
    }
  }
  loadingPage() {
    this.getStationList();
    // this.getDataSynopticForGraph();
  }

  getStationList() {
    const data = {
      limit: this.limit,
      page: this.page,
      keyword: this.keyword,
    };
    console.log("get start item and search", data);
    this.Spinner.show();
    this.serviceGetstation.getsynoticList(data).subscribe((res) => {
      this.Spinner.hide();

      this.datalist = res.data;
      console.log("getStationList", this.datalist);
    }),
      (err) => {
        console.log(err);
      };
  }

  getsynoticBystationId() {
    const data = {
      limit: (this.limit = 3),
      page: this.page,
      station_id: this.stationId,
    };
    console.log("get station sonitic by station", data);
    this.Spinner.show();
    try {
      this.synotic.getSynoticBystation(data).subscribe(res => {
        console.log('get station by idsssssssssss', res);
        this.statin_data = res.data.Data[0]
      })

    } catch (error) {

    }
  }
  getstationAllByUser() {
    this.Spinner.show();

    const data = {
      userid: this.userId,
    };
    console.log("Send data to api", data);
    this.stationservice.getStationSynopticAllByUser(data).subscribe((res) => {
      console.log("get by userder dhsfvsgvsdhfvh", res);
      this.Spinner.hide();

      this.stationListAllByUser = res.data;

      this.myCompare(this.stationListAllByUser);
    }),
      (err) => {
        console.log(err);
      };
    this.Spinner.hide();
  }

  getDataSynopticForGraph() {
    this.onClickShowGraph = true;
    const data = {
      myColumn: this.feature,
      station_id: this.stationId,
      startTime: this.startTime + " 00:00:00",
      endTime: this.endTime + " 23:59:59",
    };
    this.Spinner.show();

    this.synotic.getSynoticForGraph(data).subscribe((res) => {
      console.log(res);
      this.Spinner.hide();
      if (res.data.length === 0) {
        this.haveNOdataGraph = true;
      } else {
        this.haveNOdataGraph = false;
        this.loadDataForGraph(res);
        this.getsynoticBystationId()
        this.Spinner.hide();
      }
    }),
      (err) => {
        console.log(err);
      };
  }
  arrayColumn(array, keyTime, keyName) {
    return array.map(function (value, index) {
      let check = moment(value[keyTime], "YYYY/MM/DD H:mm");
      let month = parseInt(check.format("M")) - 1;
      let day = parseInt(check.format("D"));
      let year = parseInt(check.format("YYYY"));
      let hours = parseInt(check.format("H"));
      let minute = parseInt(check.format("mm"));
      let myData =
        value[keyName] === "E6" || value[keyName] == ""
          ? 0
          : parseFloat(value[keyName]);

      return { y: myData, x: new Date(year, month, day, hours, minute) };
    });
  }

  loadDataForGraph(data: any) {
    let myKey = data.data.key;
    let myValue = data.data.value;

    for (let index = 0; index < myKey.length; index++) {
      const myKeyLoop = myKey[index];
      let myDataPoints = this.arrayColumn(myValue, "time", myKeyLoop);
      let myObject = {
        type: "line",
        axisYType: "secondary",
        name: myKeyLoop,
        showInLegend: true,
        markerSize: 5,
        dataPoints: myDataPoints,
      };
      this.myDataForGraph.push(myObject);
    }
    this.myGraph();
  }
  onsearching(event: KeyboardEvent) {
    this.keyword = (event.target as HTMLInputElement).value;

    this.getStationList();
  }

  myCompare(dataAll: any) {
    function comparer(otherArray) {
      return function (current) {
        return (
          otherArray.filter(function (other) {
            return (
              other.id == current.id &&
              other.station_number == current.station_number
            );
          }).length == 0
        );
      };
    }

    let numAll: number = parseInt(dataAll.length);
    let myNum: number = Math.ceil(numAll / 2);
    let dataOne = dataAll.slice(0, myNum);
    let dataTwo = dataAll.slice(myNum);
    this.dataNewCompareOne = [];
    this.dataNewCompareOne = dataOne;
    this.dataNewCompareTwo = [];
    this.dataNewCompareTwo = dataTwo;
  }

  getFeature(e) {
    let myid = e.target.value;
    if (e.target.checked) {
      this.feature.push(myid);
    } else {
      for (let index = 0; index < this.feature.length; index++) {
        const element = this.feature[index];
        if (myid == element) {
          this.feature.splice(index, 1);
        }
      }
    }
  }


  clearFeature() {
    this.feature = [];
    let checkboox: any = document.getElementsByName("featur");

    for (let i = 0; i < checkboox.length; i++) {
      if (checkboox[i].type == "checkbox") {
        checkboox[i].checked = false;
      }
    }
  }
  getFeatureAll(e) {
    let checkall = e.target.checked;

    let checkboox: any = document.getElementsByName("featur");

    if (checkall) {
      for (let i = 0; i < checkboox.length; i++) {
        checkboox[i].checked = true;
      }

      let myFeature = this.feature;

      this.feature = [
        "avg_wind_speed",
        "avg_wind_direction",
        "wind_gust",
        "wind_status",
        "air_temperature",
        "relative_humidity",
        "air_pressure",
        "dewpoint",
        "metpak_status",
        "net_radiation",
        "rain_tips",
        "rain_fall",
        "ext_batt",
        "moisture",
        "modern_signal",
      ];
    } else {
      for (let index = 0; index < checkboox.length; index++) {
        checkboox[index].checked = false;
      }
      this.feature = [];
    }
  }
  changeStation(ev) {
    // console.log(ev);

    let checkboxs = document.getElementsByName("station");
    for (let index = 0; index < checkboxs.length; index++) {
      const element = checkboxs.item(index) as HTMLInputElement;
      if ((ev.target as Element) === (element as Element)) {
        element.checked = true;
        this.stationId = ev.target.value;
      } else {
        element.checked = false;
      }
    }

    console.log("my Station id", this.stationId);

    if (ev.target.checked == true) {
    }
  }

  myGraph() {
    let chart = new CanvasJS.Chart("chartContainer", {
      zoomEnabled: true,
      animationEnabled: true,
      exportEnabled: true,
      title: {
        text: "",
        fontFamily: "boonhom",
      },
      subtitles: [
        {
          // text: "Try Zooming and Panning",
        },
      ],
      axisX: [
        {
          valueFormatString: "DD-MMM H:mm",
        },
      ],
      axisY: {
        valueFormatString: "0.0#",
      },

      data: this.myDataForGraph,
    });
    chart.render();
  }
}
