import { Component, OnInit } from "@angular/core";
import { getStyle, hexToRgba } from "@coreui/coreui/dist/js/coreui-utilities";
import { CustomTooltips } from "@coreui/coreui-plugin-chartjs-custom-tooltips";
import { Router } from "@angular/router";
import { StationService } from "../../../services/station.service";
import * as Leaflet from "leaflet";
import { AuthService } from "../../../services/auth.service";
import Swal from "sweetalert2";
import { WaterserviceService } from "../../../services/waterservice.service";

@Component({
  templateUrl: "dashboard.component.html",
  styleUrls: ["./dashbord.component.scss"],
})
export class DashboardComponent implements OnInit {
  public map: Leaflet.Map;
  public page: number = 1;
  public limit: number = 10;
  public keyword: string = "";
  public stationList: any;
  public setStation: any;
  public latitude: any;
  public longitude: any;
  public alt: string;

  public adminCountAll: number = 0;
  public userCountAll: number = 0;
  public synopticCount: number;
  public hydrologyCount: number;
  public rainguageCount: number;

  constructor(
    private router: Router,
    private stationservice: StationService,
    public authService: AuthService,
    private water: WaterserviceService
  ) { }

  ngOnInit(): void {
    // generate random values for mainChart
    this.leafletMap();
    this.countUserAdmin();
    this.countUserAll();
    this.countStationHydrology();
    this.countStationSynoptic();
    this.Coutwater_station();
  }
  ngOnDestroy() {
    // this.map.remove();
  }
  countUserAll() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
    };
    // console.log("pls give me data", data);

    this.authService.getlistUser(data).subscribe((res) => {
      // console.log(res);
      if (res.status == "1") {
        this.userCountAll = res.data.Datatotal;
        // console.log("get lis oksssss", this.userCountAll);
      } else {
        this.alertfalse();
      }
    });
  }

  Coutwater_station() {
    const local = JSON.parse(localStorage.getItem("userData"));
    const data = {
      userid: local.id,
    };
    // console.log(data);
    try {
      this.water.getList_waterStation(data).subscribe((r) => {
        this.rainguageCount = r.data.length;
        // console.log("water station list", this.rainguageCount);
      });
    } catch (error) {
      console.log(error);
    }
  }

  async getstationListall() {
    await this.stationservice.getStationAll("").subscribe((res) => {
      this.stationList = res.data;
      // console.log("list all station", this.stationList);

      for (let index = 0; index < this.stationList.length; index++) {
        const element = this.stationList[index];

        // console.log("myLatssssssssss", element.lat);
        // console.log('myLongkkkkkkkkkkk', element.lng);

        let part = "";
        if (element.type == "Hydrology") {
          part = "assets/img/water2.png";
        } else if (element.type == "CLIMATE" || element.type == "SYNOPTIC") {
          part = "assets/img/skydrive.png";
        } else if (element.type == "Rain Gauge") {
          part = "assets/img/photos/myrain.png";
        }

        // console.log(part, 'part', element.type);

        const icons = Leaflet.icon({
          iconUrl: part,
          iconSize: [24, 24],
          iconAnchor: [6, 23],
          popupAnchor: [0, -20],
        });
        Leaflet.marker([element.lat, element.lng], {
          icon: icons,
          draggable: false,
          title: "test",
        })
          .addTo(this.map)
          .openTooltip()
          .setTooltipContent("test")
          .bindTooltip(element.name_lao, {
            permanent: false,
            direction: "top",
          });
      }
    });
  }

  async leafletMap() {
    this.latitude = 17.969980457645065;
    this.longitude = 102.57054450401573;
    if (!this.map) {
      this.map = Leaflet.map("mapId").setView(
        [this.latitude, this.longitude],
        6
      );
      this.map.invalidateSize();
      Leaflet.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        attribution: "edupala.com  Angular LeafLet",
      }).addTo(this.map);
      const customMarkerIcon = Leaflet.icon({
        iconUrl: "assets/img/custom-marker-icon.png",
        iconSize: [24, 24],
        iconAnchor: [6, 23],
        popupAnchor: [0, -20],
      });

      let that = this;

      that.map.panTo(new Leaflet.LatLng(this.latitude, this.longitude));

      this.getstationListall();
    }
  }

  countUserAdmin() {
    const data = {
      role: "admin",
    };

    this.authService.countUserByRole(data).subscribe((res) => {
      if (res.status == "1") {
        this.adminCountAll = res.data[0]["user"];
      } else {
      }
    });
  }

  countStationHydrology() {
    const data = {
      role: "user",
    };
    // console.log("pls give me data", data);

    this.stationservice.countHydrology(data).subscribe((res) => {
      // console.log(res);
      if (res.status == "1") {
        this.hydrologyCount = res.data[0]["total"];
      } else {
      }
    });
  }

  countStationSynoptic() {
    const data = {
      role: "user",
    };
    console.log("pls give me data", data);

    this.stationservice.countSynoptic(data).subscribe((res) => {
      // console.log(res);
      if (res.status == "1") {
        this.synopticCount = res.data[0]["total"];
      } else {
      }
    });
  }
  alertfalse() {
    Swal.fire({
      icon: "error",
      title: "Oops...",
      text: "Something went wrong!",
      // footer: '<a href="">Why do I have this issue?</a>'
    });
  }
}
