import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import Swal from "sweetalert2";
import { AuthService } from "../../../services/auth.service";
const { version: appVersion } = require("../../../../package.json");
@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.scss"],
})
export class LoginComponent implements OnInit {
  public Phonenumber: string;
  public password: string;
  public platform: string = "forecast";
  public appVersion: string = "0.0.0";
  public Setlanguse: string = localStorage.getItem('language')
  constructor(private route: Router, private authservice: AuthService) { }

  ngOnInit(): void {
    this.Phonenumber = "admin";
    this.password = "123456";
    this.appVersion = appVersion;
    // this.refresh();

  }

  login() {
    const data = {
      username: this.Phonenumber,
      password: this.password,
    };

    console.log("ss", data);
    this.authservice.login(data).subscribe((res) => {
      if (res.data.role == "admin") {
        localStorage.setItem("Token", res.Token);
        localStorage.setItem("userData", JSON.stringify(res.data));
        localStorage.setItem("role", JSON.stringify(res.data.role));
        this.route.navigate(["/dashboard"]);
        // this.alertsuccess();
      } else if (res.data.role == "user") {
        localStorage.setItem("Token", res.Token);
        localStorage.setItem("userData", JSON.stringify(res.data));
        this.route.navigate(["/forecastview"]);

        // this.alertsuccess();
      } else {
        if (this.Setlanguse === 'lao') {
          this.alertfalsealo();

        } else {
          this.alertfalsEng()
        }
      }
      //
    }),
      (err) => {
        console.log(err);
      };
  }

  alertsuccess() {
    Swal.fire("", "!", "success");
  }

  alertfalsealo() {
    Swal.fire({
      icon: "error",
      title: "",
      text: "",
      // footer: '<a href="">Why do I have this issue?</a>'
    });
  }
  alertfalsEng() {
    Swal.fire({
      icon: "error",
      title: "Oops...",
      text: "Password Wrong!",
      // footer: '<a href="">Why do I have this issue?</a>'
    });
  }
  setLanguaseLa() {
    localStorage.setItem("language", "lao");
    window.location.reload();
  }
  setLanguaseEng() {
    localStorage.setItem("language", "eng");
    window.location.reload();
  }
  keyusername(event: KeyboardEvent) {
    this.Phonenumber = (event.target as HTMLInputElement).value;
  }

  keypassword(event: KeyboardEvent) {
    this.password = (event.target as HTMLInputElement).value;
  }
}
