import { Component, OnDestroy, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import * as moment from "moment";
import { HydrologyService } from "../../services/hydrology.service";
import { StationService } from "../../services/station.service";
import { takeUntil } from "rxjs/operators";
import {
  WindRose,
  WindDescription,
  Service,
} from "../../services/chartservices";
import { SynoticService } from "../../services/synotic.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Subscription } from "rxjs";
@Component({
  selector: "app-forecastview",
  templateUrl: "./forecastview.component.html",
  styleUrls: ["./forecastview.component.scss"],
  providers: [Service],
})
export class ForecastviewComponent implements OnInit, OnDestroy {
  public isCollapsed: boolean = false;
  public checkcomming: string = "";
  windRoseData: WindRose[];
  windSources: WindDescription[];
  // public mydate = moment("20210729", "YYYYMMDD").fromNow();
  public setofpart: string = " ";
  public limit: number = 100;
  public page: number = 1;
  public keyword: string = "";
  //data
  public datalist: any;
  constructor(
    private router: Router,
    private serviceGetstation: HydrologyService,
    private stationService: StationService,
    public service: Service,
    private synoticservice: SynoticService,
    private spinner: NgxSpinnerService
  ) {
    this.windRoseData = service.getWindRoseData();
    this.windSources = service.getWindSources();
  }
  public laotime: any = moment.locale("lo");
  public dateNow: any = moment().format("llll");
  public checkAdmin: boolean = false;
  public subs: Array<Subscription> = [];
  ngOnInit(): void {
    this.loadingPage();
    // console.log("mydate", this.mydate);
  }
  ngOnDestroy() {
    this.subs.forEach((v) => {
      v.unsubscribe();
    });
  }

  loadingPage() {
    this.getStationList();
  }

  setpartlao(part: number) {
    if (part == 1) {
      this.setofpart = "";
    } else if (part == 2) {
      this.setofpart = "";
    } else if (part == 3) {
      this.setofpart = "";
    } else if (part == 4) {
      this.setofpart = "";
    }
  }
  // setpartEng(part: number) {
  //   if ((part = 1)) {
  //     this.setofpart = "";
  //   } else if ((part = 2)) {
  //     this.setofpart = "";
  //   } else if ((part = 3)) {
  //     this.setofpart = "";
  //   }
  // }
  getStationList() {
    const data = {
      limit: (this.limit = 12),
      page: this.page,
      keyword: this.keyword,
    };
    console.log("get start item and search", data);
    this.spinner.show();
    this.subs.push(
      this.serviceGetstation.getsynoticListChilldrenStation(data).subscribe(
        (res) => {
          this.datalist = res.data;
          console.log("getStationList", this.datalist);
          this.spinner.hide();
        },
        (err) => {
          console.log(err);
        }
      )
    );
  }

  getStationListBypart(part: string) {
    const data = {
      part: part,
      limit: (this.limit = 10),
      page: this.page,
      keyword: this.keyword,
    };
    console.log("get by getStationListBypart", data);

    this.serviceGetstation.getsynoticListChilldrenStationByPart(data).subscribe(
      (res) => {
        this.datalist = res.data;
        console.log("getStationList by part okokokkkok", res);
      },
      (err) => {
        console.log(err);
      }
    );
  }
  onsearching(event: KeyboardEvent) {
    this.keyword = (event.target as HTMLInputElement).value;
    console.log("key seacrh", this.keyword);

    this.getStationList();
  }

  stationdetail(id: number, coutday: number) {
    this.router.navigate(["/stationbyone"], {
      queryParams: { stationId: id, coutday: coutday },
    });
  }

  goToReport() {
    this.router.navigate(["report"]);
  }

  checkMyDate(data: any) {
    let myNum = moment(data, "YYYYMMDD, h:mm:ss a").fromNow();
    let countDate = parseInt(myNum.split(" ")[0]);

    let myDay = myNum.split(" ")[1];

    if (myDay == "") {
      let allDay = countDate * 365;
      // console.log("Year", allDay);
      return allDay;
    } else if (myDay == "") {
      let allDay = countDate * 30;
      // console.log("month", allDay);
      return allDay;
    } else if (myDay == "") {
      // console.log("day", countDate);
      return countDate;
    } else if (myDay == "") {
      // console.log("hour", countDate);
      return 1;
    } else {
      return 1;
    }
  }
}
