import { Route } from "@angular/compiler/src/core";
import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { AuthService } from "../../../services/auth.service";
import { PageingtationService } from "../../../services/pageingtation.service";
import Swal from "sweetalert2";

@Component({
  selector: "app-user-manage",
  templateUrl: "./user-manage.component.html",
  styleUrls: ["./user-manage.component.scss"],
})
export class UserManageComponent implements OnInit {
  public page: number = 1;
  public limit: number = 10;
  public keyword: string = "";

  public userlist_data: any;

  public userCountAll: number;
  public userCountAdmin: number;
  public userCountUser: number;

  constructor(
    public route: Router,
    public authService: AuthService,
    public pageingtation: PageingtationService
  ) {}

  ngOnInit(): void {
    this.loading();
  }
  loading() {
    this.getlistUser();
    this.countUserAdmin();
    this.countUserByUser();
    console.log(this.userlist_data, "test get total");
  }

  getlistUser() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
    };
    console.log("pls give me data", data);

    this.authService.getlistUser(data).subscribe((res) => {
      console.log(res);
      if (res.status == "1") {
        this.userlist_data = res.data;
        this.userCountAll = res.data.Datatotal;
        console.log("get lis oksssss", this.userlist_data.Pagetotal);
      } else {
        this.alertfalse();
      }
    });
  }
  addUser() {
    this.route.navigate(["/adduser"], {
      queryParams: { statuscheck: "adduser" },
    });
  }
  Update(id: number) {
    this.route.navigate(["/adduser"], {
      queryParams: { statuscheck: "updateuser", updateuserId: id },
    });
  }

  searchUserlist(event: KeyboardEvent) {
    this.keyword = (event.target as HTMLInputElement).value;
    this.getlistUser();
  }

  onchangeDisable(id: number, actived: number) {
    console.log("5555555", actived);
    let isActived = actived;
    if (actived == 0) {
      isActived = 1;
      console.log("11", isActived);
    } else if (actived == 1) {
      isActived = 0;
      console.log("22", isActived);
    } else {
      console.log("no have actived");
    }

    const data = {
      userid: id,
      isActive: isActived,
    };
    console.log("setdata", data);

    this.authService.setEnableOrDisableUser(data).subscribe((res) => {
      console.log(res);

      if (res.status == 1) {
        if (res.status == 1) {
          this.getlistUser();
          if (res.data.proceed == "start") {
            Swal.fire({
              icon: "success",
              title: "",
              text: "!",
              // footer: '<a href="">Why do I have this issue?</a>'
            });
          } else if (res.data.proceed == "stop") {
            Swal.fire({
              icon: "error",
              title: "",
              text: "!",
              // footer: '<a href="">Why do I have this issue?</a>'
            });
          }
        }
        this.getlistUser();
      }
    });
  }

  deleteUser(id: number) {
    Swal.fire({
      title: "?",
      text: "!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "",
    }).then((result) => {
      if (result.isConfirmed) {
        const data = {
          userid: id,
        };

        this.authService.deleteUser(data).subscribe((res) => {
          if (res.status == "1") {
            Swal.fire("!", "", "success"),
              this.getlistUser();
          }
        });
      }
    });
  }

  addRole(id: number, username: string) {
    this.route.navigate(["/stationbyuser"], {
      queryParams: { userid: id, username: username },
    });
  }

  click_page(page: any) {
    this.pageingtation.pageingtations;
    if (page) {
      this.page = page;
      this.loading();
    }
  }
  alertfalse() {
    Swal.fire({
      icon: "error",
      title: "Oops...",
      text: "Something went wrong!",
      // footer: '<a href="">Why do I have this issue?</a>'
    });
  }

  countUserAdmin() {
    const data = {
      role: "admin",
    };

    this.authService.countUserByRole(data).subscribe((res) => {
      console.log(res);
      if (res.status == "1") {
        this.userCountAdmin = res.data[0]["user"];
      } else {
      }
    });
  }

  countUserByUser() {
    const data = {
      role: "user",
    };
    console.log("pls give me data", data);

    this.authService.countUserByRole(data).subscribe((res) => {
      console.log(res);
      if (res.status == "1") {
        this.userCountUser = res.data[0]["user"];
      } else {
      }
    });
  }
}
