import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import * as moment from "moment";
import { HydrologyService } from "../../services/hydrology.service";
import * as CanvasJS from "../../assets/canvasjs-3.3.2/canvasjs.min";
import { SynoticService } from "../../services/synotic.service";
import { StationService } from "../../services/station.service";
import Swal from "sweetalert2";
import { CollapseDirective } from "ngx-bootstrap/collapse";

import { NgxSpinnerService } from "ngx-spinner";
@Component({
  selector: "app-authok-graph",
  templateUrl: "./authok-graph.component.html",
  styleUrls: ["./authok-graph.component.scss"],
  // providers: [ServiceDataChart],
})
export class AuthokGraphComponent implements OnInit {
  private _isCollapsed: boolean = true;
  @ViewChild(CollapseDirective, { read: ElementRef, static: false })
  collapse!: CollapseDirective;

  collapseRef;

  public chart: any = null;
  public userId: number;
  public date: string = moment().format("Do MMM");
  public array_id: Array<number> = [];
  public hydrology_station: any;
  public myDataForGraph: Array<any> = [];
  public station_data: any;
  public isCollapsed: boolean = false;
  public limit: number = 12;
  public page: number = 1;
  public keyword: string = "";
  public checkAdmin: boolean = false;
  //data
  public dataNewCompare: Array<any> = [];

  public dataNewCompareOne: Array<any>;
  public dataNewCompareTwo: Array<any>;

  public hydrology_data: any;
  public stationListAllByUser: any;

  public stationId: number;
  public feature: Array<string> = [];
  public startTime: string;
  public endTime: string;

  public onClickShowGraph: boolean = false;
  public haveNOdataGraph: boolean = false;
  public checkwater: boolean = false;
  public checkrainfall: boolean = false;
  public checkair: boolean = false;

  public waringlevel: string = ''
  public dangerlevel: string = ''

  public selectFeature: string = "";
  @ViewChild("myChart", {
    static: true,
  })
  myChart: {
    nativeElement: HTMLCanvasElement;
  };
  constructor(
    private router: Router,
    private hydrology: HydrologyService,
    private synotic: SynoticService,
    public stationservice: StationService,
    private spinner: NgxSpinnerService
  ) { }
  public laotime: any = moment.locale("lo");
  public dateNow: any = moment().format("llll");

  ngOnInit(): void {
    this.loadingPage();
  }
  ngAfterViewChecked(): void {
    this.collapseRef = this.collapse;
  }
  loadingPage() {
    const localsata = localStorage.getItem('language')
    this.getStationList();
    // this.chartjsAuthok();
    if (localsata === 'eng') {
      this.waringlevel = 'Warning Level'
      this.dangerlevel = 'Danger Level'

    } else {
      this.waringlevel = ''
      this.dangerlevel = ''
    }

  }
  clickLoad() {
    this.myDataForGraph = [];
    if (!this.stationId) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (!this.startTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (!this.endTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else {
      this.getDataSynopticForGraph();
    }
  }

  getStationList() {
    const localdata = JSON.parse(localStorage.getItem("userData"));
    const data = {
      userid: localdata.id,
      limit: this.limit,
      page: this.page,
      keyword: this.keyword,
    };
    console.log("get start item and search", data);
    this.spinner.show();

    this.hydrology.getHydrologyListByuser(data).subscribe((res) => {
      this.spinner.hide();

      this.hydrology_data = res.data;
      console.log("getStationList hydrology_data", this.hydrology_data);
      this.myCompare(res.data);
    });
  }

  gethydrologyByid() {
    const data = {
      station_id: this.stationId,
      limit: this.limit,
      page: this.page,
      keyword: this.keyword,
    };
    this.spinner.show();

    try {
      this.hydrology.getHydrologyListById(data).subscribe((res) => {
        this.spinner.hide();

        this.hydrology_station = res.data.Data[0];
        console.log("get hydrology bysctaion", this.hydrology_station);
      });
    } catch (error) {
      console.log(error);
      this.spinner.hide();
    }
  }
  getfeature(featrue: number) {
    this.checkair = false;
    this.checkrainfall = false;
    this.checkwater = false;
    if (featrue == 1) {
      this.feature = [];
      this.selectFeature = "";
      const data = "water_fall";
      this.feature.push(data);
      console.log("send to server:", this.feature);
    } else if (featrue == 2) {
      this.selectFeature = "";

      const data = "rain_fall";
      this.feature = [];
      this.feature.push(data);
      console.log("send to server:", this.feature);
    } else if (featrue == 3) {
      this.selectFeature = "";

      const data = "water_temperature";
      this.feature = [];
      this.feature.push(data);
      console.log("send to server:", this.feature);
    } else if (featrue == 4) {
      this.selectFeature = "";

      const featureall = ["rain_fall", "water_fall", "water_temperature"];
      this.feature = [];
      this.feature = featureall;
      console.log("send to server:", this.feature);
    }
  }
  getDataSynopticForGraph() {
    this.getOnlystationHydrology();

    this.onClickShowGraph = true;
    const data = {
      myColumn: this.feature,
      station_id: this.stationId,
      startTime: this.startTime + " 00:00:00",
      endTime: this.endTime + " 23:59:59",
    };
    console.log("send to server:", data);

    this.spinner.show();
    this.hydrology.getHydrologyForGraph(data).subscribe((res) => {
      console.log("wowowowowwowowo", res);
      this.gethydrologyByid();

      this.spinner.hide();
      // let data = JSON.parse(r.data[0]);
      const getdata = res;
      console.log("get hydrology", getdata);
      console.log("haveNOdataGraph", getdata);
      if (res.data.length === 0) {
        this.AlertNOhavedata();
        this.router.navigate(["/climate-station"]);
        this.haveNOdataGraph = false;
      } else {
        this.haveNOdataGraph = true;
        console.log("haveNOdataGraph", this.haveNOdataGraph);

        // this.chartjsAuthok(res.data.value);

        this.loadDataForGraph(res);
      }
    }),
      (err) => {
        console.log(err);
        this.AlertNOhavedata();
      };
  }

  AlertNOhavedata() {
    Swal.fire({
      icon: "error",
      title: "Oops...",
      text: "!",
      // footer: '<a href="">Why do I have this issue?</a>'
    });
  }
  changeStation(ev) {
    let checkboxs = document.getElementsByName("station");
    for (let index = 0; index < checkboxs.length; index++) {
      const element = checkboxs.item(index) as HTMLInputElement;
      if ((ev.target as Element) === (element as Element)) {
        element.checked = true;
        this.stationId = ev.target.value;
      } else {
        element.checked = false;
      }
    }

    console.log("my Station id", this.stationId);
    if (ev.target.checked == true) {
    }
  }

  myGramp() {
    let chart = new CanvasJS.Chart("chartContainer", {
      zoomEnabled: true,
      animationEnabled: true,
      exportEnabled: true,
      title: {
        text: "Hydrology Station",
        fontFamily: "boonhom",
      },
      toolTip: {
        fontFamily: "boonhom",
        enabled: true, //disable here
        animationEnabled: true, //disable here
      },

      axisX: [
        {
          // valueFormatString: "DD-MMM HH:mm"
          valueFormatString: "DD-MMM-H:mm",
        },
      ],

      axisY: {
        valueFormatString: "0.0# mm",
      },

      data: this.myDataForGraph,
    });

    chart.render();
  }
  arrayColumn(array, keyTime, keyName) {
    return array.map(function (value, index) {
      let check = moment(value[keyTime], "YYYY/MM/DD H:mm");
      let month = parseInt(check.format("M")) - 1;
      let day = parseInt(check.format("D"));
      let year = parseInt(check.format("YYYY"));
      let hours = parseInt(check.format("H"));
      let minute = parseInt(check.format("mm"));
      let myData =
        value[keyName] === "E6" || value[keyName] == ""
          ? 0
          : parseFloat(value[keyName]);
      // let myData = parseFloat(value[keyName] == "E6" ? '0' : value[keyName]);

      return { y: myData, x: new Date(year, month, day, hours, minute) };
    });
  }

  arrayColumnFix(array, keyTime, number: number) {
    return array.map(function (value, index) {
      let check = moment(value[keyTime], "YYYY/MM/DD H:mm");
      let month = parseInt(check.format("M")) - 1;
      let day = parseInt(check.format("D"));
      let year = parseInt(check.format("YYYY"));
      let hours = parseInt(check.format("H"));
      let minute = parseInt(check.format("mm"));

      // let myData = parseFloat(value[keyName] == "E6" ? '0' : value[keyName]);

      return { y: number, x: new Date(year, month, day, hours, minute) };
    });
  }

  loadDataForGraph(data: any) {
    console.log("test mydata", this.station_data);
    console.log("test mydata", data);

    let myKey = data.data.key;
    let myValue = data.data.value;
    const lertwl = parseInt(this.station_data.alert);
    const alarmwl = parseInt(this.station_data.alarm);

    let dataAlert = this.arrayColumnFix(myValue, "time", lertwl);
    let dataAlarm = this.arrayColumnFix(myValue, "time", alarmwl);
    console.log("dataAlert", dataAlert);
    console.log("dataAlarm", dataAlarm);
    console.log("dataAlarm", myKey);

    let myObject = {
      type: "line",
      // axisYType: "success",
      name: "Danger Level" + ":" + dataAlert[0].y + "mm",
      lineColor: "red",
      color: "red",
      showInLegend: true,
      markerSize: 1,
      dataPoints: dataAlert,
    };

    this.myDataForGraph.push(myObject);
    let myObject2 = {
      name: "Warnning Level" + ":" + dataAlarm[0].y + "mm",

      type: "line",
      lineColor: "gold",
      // name:"tesss",
      cursor: "pointer",
      color: "#FCC249",

      // axisYType: "success",
      showInLegend: true,
      markerSize: 1,
      dataPoints: dataAlarm,
    };

    this.myDataForGraph.push(myObject2);

    for (let index = 0; index < myKey.length; index++) {
      const myKeyLoop = myKey[index];
      let myDataPoints = this.arrayColumn(myValue, "time", myKeyLoop);
      console.log("myDataPoints", myKeyLoop);
      let myColor = "";
      if (myKeyLoop === "water_fall") {
        this.checkwater = true;
        myColor = "#2055e6";
      } else if (myKeyLoop === "rain_fall") {
        myColor = "#59e3b5";
        this.checkrainfall = true;
      } else if (myKeyLoop === "water_temperature") {
        myColor = "#86ab22";
        this.checkair = true;
      }

      // else if (myKeyLoop === "water_fall"||"rain_fall"||"water_temperature") {

      // }

      let myObject = {
        type: "line",
        // axisYType: "secondary",
        name:
          myKeyLoop === "water_fall"
            ? "water_level"
            : myKeyLoop + ":" + myDataPoints[0].y + "mm",
        showInLegend: true,
        lineColor: myColor,
        color: myColor,
        markerSize: 2,
        cursor: "pointer",
        dataPoints: myDataPoints,
      };
      this.myDataForGraph.push(myObject);
      console.log("this.myDataForGraph", this.myDataForGraph);
    }

    this.myGramp();
  }
  getOnlystationHydrology() {
    const data = {
      id: this.stationId,
    };
    // console.log("test mydata", data);

    this.stationservice.getById(data).subscribe((res) => {
      // console.log("station by id", res);
      this.station_data = res.data[0];
    }),
      (err) => {
        console.log(err);
        this.AlertNOhavedata();
      };
  }
  myCompare(dataAll: any) {
    function comparer(otherArray) {
      return function (current) {
        return (
          otherArray.filter(function (other) {
            return (
              other.id == current.id &&
              other.station_number == current.station_number
            );
          }).length == 0
        );
      };
    }

    // console.log("All", dataAll);

    let numAll: number = parseInt(dataAll.length);
    let myNum: number = Math.ceil(numAll / 2);
    let dataOne = dataAll.slice(0, myNum);
    let dataTwo = dataAll.slice(myNum);
    this.dataNewCompareOne = [];
    this.dataNewCompareOne = dataOne;

    this.dataNewCompareTwo = [];
    this.dataNewCompareTwo = dataTwo;

    // console.log(this.dataNewCompareOne);
    // console.log(this.dataNewCompareTwo);
  }
}
