import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import * as moment from "moment";
import { HydrologyService } from "../../services/hydrology.service";
import Swal from "sweetalert2";
import { AppServiceCsv } from "../../services/mycsv";
import { ReportfilecsvService } from "../../services/reportfilecsv.service";
import { XlsxService } from "../../services/xlsx.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-authok-report",
  templateUrl: "./authok-report.component.html",
  styleUrls: ["./authok-report.component.scss"],
})
export class AuthokReportComponent implements OnInit {
  public isCollapsed: boolean = false;
  public page: number = 1;
  public limit: number = 100;
  public keyword: string = "";
  public feature: Array<string> = [];

  public station_data: any;
  public Myselected: boolean;
  public stationListAllOne: Array<any>;
  public stationListAllTwo: Array<any>;
  public synotic_data_report: any;

  //data
  public dateNow: any = moment().format("llll");
  public datalist: any;
  public station_id: string;
  public array_id: Array<number> = [];
  public userId: number;
  public startTime: string;
  public endTime: string;
  public checkAdmin: boolean = false;
  constructor(
    private router: Router,
    public hydrology: HydrologyService,
    private appService: AppServiceCsv,
    public reportservice: ReportfilecsvService,
    public xlsx: XlsxService,
    private Spinner: NgxSpinnerService
  ) {}
  ngOnInit(): void {
    const local = JSON.parse(localStorage.getItem("userData"));
    this.userId = local.id;
    if (local.role === "admin") {
      this.checkAdmin = true;
    } else {
      this.checkAdmin = false;
    }
    this.getListstationAll();
  }
  changeGender(e) {
    console.log("changeGender");

    let idchange = e.target.value;
    let myid = parseInt(idchange);
    if (e.target.checked) {
      this.array_id.push(myid);
    } else {
      for (let index = 0; index < this.array_id.length; index++) {
        const element = this.array_id[index];
        if (myid == element) {
          this.array_id.splice(index, 1);
        }
      }
    }
    console.log("ssssssssssssss", this.array_id);
  }
  getListstationAll() {
    const localdata = JSON.parse(localStorage.getItem("userData"));
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
      userid: localdata.id,
    };

    this.hydrology.getHydrologyListByuser(data).subscribe((res) => {
      console.log("getHydrologyListByuser", res);
      if (res.status == 1) {
        this.station_data = res.data;
        console.log(this.station_data);
        let result = res.data;
        let numAll: number = parseInt(result.length);
        let myNum: number = Math.ceil(numAll / 2);
        let dataOne = result.slice(0, myNum);
        let dataTwo = result.slice(myNum);
        this.stationListAllOne = [];
        this.stationListAllOne = dataOne;
        this.stationListAllTwo = [];
        this.stationListAllTwo = dataTwo;
      }
    });
  }

  checkallBytone(e) {
    console.log(e);
    let checkall = e.target.checked;
    let checkboox = document.getElementsByName("input");
    if (checkall) {
      // for (let i = 0; i < checkboox.length; i++) {
      //   if (checkboox[i].type == "checkbox") {
      //     checkboox[i].checked = true;
      //   }
      // }
    }
  }

  clickLselectAll(e) {
    let checkall = e.target.checked;

    let checkboox: any = document.getElementsByName("station");

    if (checkall) {
      for (let i = 0; i < checkboox.length; i++) {
        checkboox[i].checked = true;
      }

      let idStation = this.station_data;
      console.log("idStation", idStation);
      for (let index = 0; index < idStation.length; index++) {
        const element = idStation[index];
        console.log("test", element.id);
        this.array_id.push(element.id);
      }
    } else {
      for (let index = 0; index < checkboox.length; index++) {
        checkboox[index].checked = false;
      }
      this.array_id = [];
    }
  }

  checkfromReportExcel() {
    if (this.array_id.length == 0) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (this.feature.length == 0) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (!this.startTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (!this.endTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else {
      this.exportToexcel();
    }
  }

  getFeature(e) {
    let myid = e.target.value;
    if (e.target.checked) {
      this.feature.push(myid);
    } else {
      for (let index = 0; index < this.feature.length; index++) {
        const element = this.feature[index];
        if (myid == element) {
          console.log("element", element);

          this.feature.splice(index, 1);
        }
      }
    }
  }

  clearFeature() {
    this.feature = [];
    let checkboox: any = document.getElementsByName("featur");

    for (let i = 0; i < checkboox.length; i++) {
      if (checkboox[i].type == "checkbox") {
        checkboox[i].checked = false;
      }
    }
  }
  getFeatureAll(e) {
    let checkall = e.target.checked;

    let checkboox: any = document.getElementsByName("featur");

    if (checkall) {
      for (let i = 0; i < checkboox.length; i++) {
        checkboox[i].checked = true;
      }

      let myFeature = this.feature;

      this.feature = [
        "rain_tips",
        "rain_fall",
        "water_fall",
        "water_temperature",
        "ext_batt",
        "moisture",
        "modern_signal",
      ];
    } else {
      for (let index = 0; index < checkboox.length; index++) {
        checkboox[index].checked = false;
      }
      this.feature = [];
    }
  }

  checkfromReportCSV() {
    if (this.array_id.length == 0) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (!this.startTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (this.feature.length == 0) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else if (!this.endTime) {
      Swal.fire({
        icon: "warning",
        title: "!",
        text: "",
        showConfirmButton: true,
      });
    } else {
      this.exportTocsv();
    }
  }
  exportToexcel() {
    const id = this.array_id;
    const data = {
      station_id: id.map(Number).toString(),
      startTime: this.startTime + " 00:00:00",
      endTime: this.endTime + " 23:59:59",
      myColumn: this.feature,
    };
    this.Spinner.show();
    console.log("Send data to server", data);
    this.hydrology.reportHydrology(data).subscribe((res) => {
      console.log("2222222222222222222222222222222", res);
      this.Spinner.hide();

      if (res.status == 1) {
        this.xlsx.exportExcelBysheetHydrology(res.data.value);
      } else {
        this.router.navigate(["/climate-report"]);
        Swal.fire({
          icon: "warning",
          title: "!",
          text: " ",
          showConfirmButton: true,
        });
      }
    }),
      (err) => {
        console.log(err);
      };
  }
  exportTocsv() {
    const id = this.array_id;
    const data = {
      station_id: id.map(Number).toString(),
      startTime: this.startTime + " 00:00:00",
      endTime: this.endTime + " 23:59:59",
      myColumn: this.feature,
    };
    console.log("Send data to server", data);
    this.Spinner.show();

    this.hydrology.reportHydrologyCSV(data).subscribe((res) => {
      this.Spinner.hide();

      console.log("CSSSVVV", res);
      if (res.status == 1) {
        // this.synotic_data_report = res.data;
        this.xlsx.downloadFileCSV(res.data.value);
      } else {
        this.router.navigate(["/climate-report"]);
        Swal.fire({
          icon: "warning",
          title: "!",
          text: " ",
          showConfirmButton: true,
        });
      }
    });
  }
}

export class Station {
  altitude: string;
  id: number;
  laoName: string;
  latitude: string;
  longitude: string;
  name_eng: string;
  part: string;
  station_model: string;
  station_number: string;
  station_phonenumber: string;
  type: string;
}
