import { Component, Input, OnInit, Output, EventEmitter } from "@angular/core";
import { Subscription } from "rxjs";
import { StationService } from "../../services/station.service";
import Swal from "sweetalert2";

@Component({
  selector: "app-forcastshownow-menu",
  templateUrl: "./forcastshownow-menu.component.html",
  styleUrls: ["./forcastshownow-menu.component.scss"],
})
export class ForcastshownowMenuComponent implements OnInit {
  public isCollapsed: boolean = false;
  @Input() data_cart: any;
  @Input() mysatation: string;
  @Input() Active: any;

  @Output() newItemEvent = new EventEmitter<string>();
  public page: number = 1;
  public limit: number;
  public keyword: string = "";

  public station_data: any;
  public subs: Array<Subscription> = [];
  constructor(public stationservice: StationService) { }

  ngOnInit(): void {
    // this.getListall;
  }

  onsearching(event: KeyboardEvent) {
    this.keyword = (event.target as HTMLInputElement).value;
    console.log("key seacrh", this.keyword);
  }
  ngOnDestroy() {
    this.subs.forEach((v) => {
      v.unsubscribe();
    });
  }
  setLanguaseLa() {
    localStorage.setItem("language", "lao");
    window.location.reload();
  }
  setLanguaseEng() {
    localStorage.setItem("language", "eng");
    window.location.reload();
  }
  getListall() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
    };
    this.subs.push(
      this.stationservice.getListSynotic(data).subscribe(
        (res) => {
          console.log(res);
          if (res.status == 1) {
            this.station_data = res;
          } else {
            this.AlertNOhavedata();
          }
        },
        (err) => {
          console.log(err);
          this.AlertNOhavedata();
        }
      )
    );
  }

  ReloadPage() {
    window.location.reload();
  }
  changId(id: string) {
    console.log("muid", id);
    this.newItemEvent.emit(id);
  }

  AlertNOhavedata() {
    Swal.fire({
      icon: "error",
      title: "Oops...",
      text: " !",
      // footer: '<a href="">Why do I have this issue?</a>'
    });
  }
}
