import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { HydrologyService } from "../../services/hydrology.service";
import * as moment from "moment";

@Component({
  selector: "app-authok-detail",
  templateUrl: "./authok-detail.component.html",
  styleUrls: ["./authok-detail.component.scss"],
})
export class AuthokDetailComponent implements OnInit {
  public limit: number = 12;
  public page: number = 1;
  public keyword: string = "";

  public water_fall: string;
  public rain_fall: string;
  public rain_tips: string;
  public ext_batt: string;
  public modern_signal: string;
  public moisture: string;
  public water_temperature: string;

  public liststation_data: any;

  public coutday: number;
  public dateupdateData: string = "";

  public laotime: any = moment.locale("lo");
  public dateNow: any = moment().format("LLLL");
  constructor(
    private hydrology: HydrologyService,
    private route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.loading();
  }

  loading() {
    this.route.queryParams.subscribe((params) => {
      this.getStationByid(params.station_id);
      this.coutday = params.coutdate;
      console.log(params.station_id);
    });
  }
  onsearching(event: KeyboardEvent) {
    this.keyword = (event.target as HTMLInputElement).value;
    console.log("key seacrh", this.keyword);
  }

  getStationByid(id: number) {
    console.log("tetststst");

    const data = {
      page: this.page,
      limit: (this.limit = 2),
      keyword: this.keyword,
      station_id: id,
    };

    this.hydrology.getHydrologyListById(data).subscribe((res) => {
      console.log("my res", res);
      this.dateupdateData = moment(
        res.data.Data[0].time,
        "YYYYMMDD, h:mm:ss a"
      ).fromNow();
      const data = res.data.Data;

      this.liststation_data = res.data;
      this.loadingCheck(data);
    }),
      (err) => {
        console.log(err);
      };
  }

  loadingCheck(data: any) {
    console.log("testtt", data);

    this.check_rain_fall(data);
    this.check_rain_tips(data);
    this.check_water_fall(data);
    this.check_moisture(data);
    this.check_ext_batt(data);
    this.check_water_temperature(data);
  }

  check_rain_fall(data: HydrologyMOdel) {
    const myarr = data;
    let check1 = data[0].rain_fall;
    let check2 = data[1].rain_fall;

    console.log("mydata", myarr);
    if (check1 > check2) {
      this.rain_fall = "is_Up";
    } else if (check1 < check2) {
      this.rain_fall = "is_Down";
    } else if (check1 === check2) {
      this.rain_fall = "is_normal";
    } else {
      console.log("faileto checked ");
    }
  }
  check_rain_tips(data: HydrologyMOdel) {
    const myarr = data;
    let check1 = data[0].rain_tips;
    let check2 = data[1].rain_tips;

    console.log("mydata", myarr);
    if (check1 > check2) {
      this.rain_tips = "is_Up";
    } else if (check1 < check2) {
      this.rain_tips = "is_Down";
    } else if (check1 === check2) {
      this.rain_tips = "is_normal";
    } else {
      console.log("faileto checked ");
    }
  }
  check_water_fall(data: HydrologyMOdel) {
    const myarr = data;
    console.log("checkda", myarr);

    let check1 = data[0].water_fall;
    let check2 = data[1].water_fall;

    console.log("mydata", myarr);
    if (check1 > check2) {
      this.water_fall = "is_Up";
    } else if (check1 < check2) {
      this.water_fall = "is_Down";
    } else if (check1 === check2) {
      this.water_fall = "is_normal";
    } else {
      console.log("faileto checked ");
    }
  }
  check_moisture(data: HydrologyMOdel) {
    const myarr = data;
    let check1 = data[0].moisture;
    let check2 = data[1].moisture;
    console.log("mydata", myarr);
    if (check1 > check2) {
      this.moisture = "is_Up";
    } else if (check1 < check2) {
      this.moisture = "is_Down";
    } else if (check1 === check2) {
      this.moisture = "is_normal";
    } else {
      console.log("faileto checked ");
    }
  }
  check_ext_batt(data: HydrologyMOdel) {
    const myarr = data;
    let check1 = data[0].ext_batt;
    let check2 = data[1].ext_batt;
    console.log("mydata", myarr);
    if (check1 > check2) {
      this.ext_batt = "is_Up";
    } else if (check1 < check2) {
      this.ext_batt = "is_Down";
    } else if (check1 === check2) {
      this.ext_batt = "is_normal";
    } else {
      console.log("faileto checked ");
    }
  }
  check_water_temperature(data: HydrologyMOdel) {
    let check1 = data[0].water_temperature;
    let check2 = data[1].water_temperature;

    if (check1 > check2) {
      this.water_temperature = "is_Up";
    } else if (check1 < check2) {
      this.water_temperature = "is_Down";
    } else if (check1 === check2) {
      this.water_temperature = "is_normal";
    } else {
      console.log("faileto checked ");
    }
  }
  checkMyDate(data: any) {
    let myNum = moment(data, "YYYYMMDD, h:mm:ss a").fromNow();
    let countDate = parseInt(myNum.split(" ")[0]);

    let myDay = myNum.split(" ")[1];

    if (myDay == "") {
      let allDay = countDate * 365;
      // console.log("Year", allDay);
      return allDay;
    } else if (myDay == "") {
      let allDay = countDate * 30;
      // console.log("month", allDay);
      return allDay;
    } else if (myDay == "") {
      // console.log("day", countDate);
      return countDate;
    } else if (myDay == "") {
      // console.log("hour", countDate);
      return 1;
    } else {
      return 1;
    }
  }
}

class ArrayData {
  Data: HydrologyMOdel;
}

class HydrologyMOdel {
  alt: string;
  ext_batt: string;
  id: string;
  lat: string;
  lng: string;
  modern_signal: string;
  moisture: string;
  name_eng: string;
  name_lao: string;
  rain_fall: string;
  rain_tips: string;
  sid: string;
  station_id: string;
  station_number: string;
  time: string;
  type: string;
  water_fall: string;
  water_temperature: string;
}
