import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { APIService } from "./api.service";

@Injectable({
  providedIn: "root",
})
export class ReportfilecsvService extends APIService {
  public url: string = this.getBaseUrl();
  public token = "";

  //REPORT SYNOTIC
  reportSynopticChildrenStation(param: any): Observable<any> {
    const header = this.headerBase("reportSynopticChildrenStationAndFeture");
    return this.http.post(this.url + "report.api.php", param, {
      headers: header,
      reportProgress: true,
      observe: "events",
      // responseType: "blob",
    });
  }
  getsynoticListByAnyStationAndTime(param: any): Observable<any> {
    const header = this.headerBase(
      "reportSynopticByAnyStationAndTimeAndFeture"
    );
    return this.http.post(this.url + "report.api.php", param, {
      headers: header,
    });
  }

  // REPORT HYDROLOGY
  gethydrologyListByAnyStationAndTime(param: any): Observable<any> {
    const header = this.headerBase("reportHydrologyByAnyStationAndTime");
    return this.http.post(this.url + "report.api.php", param, {
      headers: header,
      reportProgress: true,
      observe: "events",
    });
  }
}
