import { Component, OnInit, ElementRef, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { StationService } from "../../../services/station.service";
import Swal from "sweetalert2";
import { PageingtationService } from "../../../services/pageingtation.service";
import { XlsxService } from "../../../services/xlsx.service";

@Component({
  selector: "app-station",
  templateUrl: "./station.component.html",
  styleUrls: ["./station.component.scss"],
})
export class StationComponent implements OnInit {
  @ViewChild("htmlData") htmlData: ElementRef;
  setDataUpdate: any = {};
  keyusername: any = {};
  selectedOption = "";
  public page: number = 1;
  public limit: number = 10;
  public keyword: string = "";

  public checkexportPDF: boolean = false;
  public station_data: Array<StationS>;

  public stationCountAll: number;
  public stationCountNorth: number;
  public stationCountCenter: number;
  public stationCountSouth: number;
  public export_data: any;
  public station: Array<any>;
  public station_resport: Array<StationS>;

  constructor(
    public router: Router,
    public stationservice: StationService,
    public pageingtation: PageingtationService,
    private exportService: XlsxService
  ) {}

  ngOnInit(): void {
    this.loading();
  }

  loading() {
    this.getListall();
    this.getStationByPartNorth();
    this.getStationByPartCenter();
    this.getStationByPartSouth();
  }

  getListall() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
    };
    this.stationservice.getStationPage(data).subscribe((res) => {
      console.log(res);
      if (res.status == 1) {
        this.station_data = res;

        console.log("sssssssssssssssssss", this.station_data);

        this.stationCountAll = res.data.Datatotal;
      }
    });
  }

  getListallForExport() {
    const data = {
      page: "",
      limit: "",
      keyword: this.keyword,
    };
    this.stationservice.getStationAll(data).subscribe((res) => {
      console.log(res);
      if (res.status == 1) {
        this.exportService.exportToExcelFileListStation(res.data);
      } else {
        console.log("no have data");
      }
    });
  }

  onsearching(ev: any) {
    this.keyword = (event.target as HTMLInputElement).value;
    this.getListall();
  }

  click_pagemodalTo(page: any) {
    this.pageingtation.pageingtations;
    if (page) {
      this.page = page;
      this.loading();
    }
  }
  deletestation(id: number) {
    Swal.fire({
      title: "?",
      text: "!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "",
      cancelButtonText: "",
    }).then((result) => {
      if (result.isConfirmed) {
        const data = {
          id: id,
        };
        this.stationservice.deletestation(data).subscribe((res) => {
          console.log(res);
          if (res.status == 1) {
            Swal.fire({
              icon: "success",
              title: "",
              showConfirmButton: false,
              timer: 1500,
            });
            this.getListall();
          } else {
            Swal.fire({
              icon: "error",
              title: "!",
              text: res.message,
              showConfirmButton: true,
            });
            this.getListall();
          }
        });
      }
    });
  }
  updateEmployee() {}
  addStation(id: number) {
    this.router.navigate(["/addstation"], {
      queryParams: { statuscheck: "addstation" },
    });
  }
  updatestation(id: number) {
    this.router.navigate(["/addstation"], {
      queryParams: { statuscheck: "updatestation", stationId: id },
    });
  }

  getStationByPartNorth() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
      part: "north",
    };
    this.stationservice.getStationBypart(data).subscribe((res) => {
      console.log(res);
      if (res.status == 1) {
        this.stationCountNorth = res.data.Datatotal;
      }
    });
  }

  getStationByPartCenter() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
      part: "center",
    };
    this.stationservice.getStationBypart(data).subscribe((res) => {
      console.log(res);
      if (res.status == 1) {
        this.stationCountCenter = res.data.Datatotal;
      }
    });
  }

  getStationByPartSouth() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword,
      part: "south",
    };
    this.stationservice.getStationBypart(data).subscribe((res) => {
      console.log(res);
      if (res.status == 1) {
        this.stationCountSouth = res.data.Datatotal;
      }
    });
  }

  public getListallForExportPDFs(): void {
    this.router.navigate(["/exportpdf"]);
  }
}

export class StationS {
  alarm: string;
  alert: string;
  alt: string;
  critical: string;
  id: string;
  lat: string;
  lng: string;
  name_eng: string;
  name_lao: string;
  part: string;
  station_model: string;
  station_number: string;
  station_phonenumber: string;
  type: string;
}
