import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { APIService } from "./api.service";
@Injectable({
  providedIn: 'root'
})
export class AuthService extends APIService {
  public url: string = this.getBaseUrl();
  public token = '';


  login(param: any): Observable<any> {
    const header = this.headerBase('login');
    return this.http.post(this.url + 'login.api.php', param, { headers: header });
  }

  createUser(param: any): Observable<any> {
    const header = this.headerBase('addUser');
    return this.http.post(this.url + 'user.api.php', param, { headers: header });
  }
  updateUser(param: any): Observable<any> {
    const header = this.headerBase('updateUser');
    return this.http.post(this.url + 'user.api.php', param, { headers: header });
  }

  deleteUser(param: any): Observable<any> {
    const header = this.headerBase('deleteUser');
    return this.http.post(this.url + 'user.api.php', param, { headers: header });
  }

  changePassword(param: any): Observable<any> {
    const header = this.headerBase('changePassword');
    return this.http.post(this.url + 'user.api.php', param, { headers: header });
  }
  setNewrole(param: any): Observable<any> {
    const header = this.headerBase('setNewRole');
    return this.http.post(this.url + 'user.api.php', param, { headers: header });
  }
  setEnableOrDisableUser(param: any): Observable<any> {
    const header = this.headerBase('setDisableAndEnableUser');
    return this.http.post(this.url + 'user.api.php', param, { headers: header });
  }

  getlistUser(param: any): Observable<any> {
    const header = this.headerBase('userListPage');
    return this.http.post(this.url + 'user.api.php', param, { headers: header });
  }
  getUserById(param: any): Observable<any> {
    const header = this.headerBase('getUser');
    return this.http.post(this.url + 'user.api.php', param, { headers: header });
  }
  createRole(param: any): Observable<any> {
    const header = this.headerBase('createRole');
    return this.http.post(this.url + 'user.api.php', param, { headers: header });
  }
  deleteRole(param: any): Observable<any> {
    const header = this.headerBase('deleteRole');
    return this.http.post(this.url + 'user.api.php', param, { headers: header });
  }

  countUserByRole(param: any): Observable<any> {
    const header = this.headerBase('countUserByRole');
    return this.http.post(this.url + 'user.api.php', param, { headers: header });
  }
}