import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { StationService } from '../../../services/station.service';
import Swal from 'sweetalert2';
import { AuthService } from '../../../services/auth.service';
import { PageingtationService } from '../../../services/pageingtation.service';

@Component({
  selector: 'app-stationbyuser',
  templateUrl: './stationbyuser.component.html',
  styleUrls: ['./stationbyuser.component.scss']
})
export class StationbyuserComponent implements OnInit {

  public page: number = 1
  public limit: number = 10
  public keyword: string = ''

  public station_data: Array<Station>
  public station_databyUser: any
  public userId: number

  public dataNewCompare: Array<any> = [];

  public dataNewCompareOne: Array<any>;
  public dataNewCompareTwo: Array<any>;

  public stationListAllByUser: Array<any>;

  public station: Array<any>


  public username: string;


  constructor(
    public router: Router,
    public stationservice: StationService,
    public auth: AuthService,
    public route: ActivatedRoute,
    public pageingtation: PageingtationService

  ) { }

  ngOnInit(): void {
    this.loading()
    this.getstationById()
  }

  loading() {
    this.route.queryParams.subscribe((params) => {
      this.userId = params.userid
      this.username = params.username
      this.getstationById()

    });
  }



  getstationById() {
    const data = {
      userid: this.userId,
      page: this.page,
      limit: this.limit,
      keyword: this.keyword
    }
    console.log('Send data to api', data);

    this.stationservice.getByuser(data).subscribe
      (res => {
        console.log('get by userder', res);
        this.station_databyUser = res

        this.getListall();
      })
  }

  getListall() {
    const data = {
      page: this.page,
      limit: this.limit,
      keyword: this.keyword
    }
    this.stationservice.getStation(data).subscribe
      (res => {
        console.log(res);
        if (res.status == 1) {
          this.station_data = res.data

          this.getstationAllByUser();
        }
      })
  }


  getstationAllByUser() {
    const data = {
      userid: this.userId
    }
    console.log('Send data to api', data);

    this.stationservice.getStationAllByUser(data).subscribe
      (res => {
        console.log('get by userder', res);
        this.stationListAllByUser = res.data

        this.myCompare(this.station_data, this.stationListAllByUser);

      })
  }

  onsearchingmodal(event: KeyboardEvent) {
    this.keyword = (event.target as HTMLInputElement).value
    console.log(this.keyword);

    this.getListall()
  }
  onsearchingbyuser(event: KeyboardEvent) {
    this.keyword = (event.target as HTMLInputElement).value
    console.log(this.keyword);

    this.getstationById()
  }
  addStations(item: any) {
    const data = {
      userid: this.userId,
      station_id: item.id
    }
    this.auth.createRole(data).subscribe
      (res => {
        console.log(res);
        if (res.status == 1) {
          Swal.fire({
            position: 'top-end',
            icon: 'success',
            title: '',
            showConfirmButton: false,


            timer: 1500
          })
          this.getstationById();
        } else if (res.status == 0) {
          Swal.fire({
            icon: 'error',
            title: '...',
            text: ' !',
            confirmButtonText: ''

            // footer: '<a href="">Why do I have this issue?</a>'
          })
        }
      }, err => {
        console.log(err);
      })
  }

  click_page(page: any) {
    this.pageingtation.pageingtations
    if (page) {
      this.page = page;
      this.loading();

    }

  }
  click_pagemodalTo(page: any) {
    console.log('tetststststststststst')
    this.pageingtation.pageingtations
    if (page) {
      this.page = page;
      this.loading();

    }
  }
  deletestation(id: number) {
    Swal.fire({
      title: '?',
      text: "!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      cancelButtonText: '',

      confirmButtonText: ''
    }).then((result) => {
      if (result.isConfirmed) {
        const data = {
          userid: this.userId,
          station_id: id
        }
        console.log('item id', id);

        this.auth.deleteRole(data).subscribe
          (res => {
            console.log(res);
            if (res.status == 1) {
              Swal.fire(
                '!',
                '',
                'success'
              )
              this.getstationById()

            } else {
              console.log('faile delete');
              this.getstationById()
            }
          })
      }
    })
  }

  myCompare(dataAll: any, dataOfUser: any) {

    function comparer(otherArray) {
      return function (current) {
        return otherArray.filter(function (other) {
          return other.id == current.id && other.station_number == current.station_number
        }).length == 0;
      }
    }

    console.log('All', dataAll);
    console.log('User', dataOfUser);

    let result = dataAll.filter(comparer(dataOfUser));

    console.log('Result Compare', result);

    this.dataNewCompare = [];

    this.dataNewCompare = result;

    let numAll: number = parseInt(result.length);
    let myNum: number = Math.ceil(numAll / 2);


    let dataOne = result.slice(0, myNum)
    let dataTwo = result.slice(myNum);

    this.dataNewCompareOne = [];
    this.dataNewCompareOne = dataOne;

    this.dataNewCompareTwo = [];
    this.dataNewCompareTwo = dataTwo;


  }

}


export class Station {
  altitude: string
  id: number
  laoName: string
  latitude: string
  longitude: string
  name: string
  station: string
}