import { Component } from "@angular/core";
import { navItems } from "../../_nav";
import { laoNav } from "../../_nav";

import { Router } from "@angular/router";

@Component({
  selector: "app-dashboard",
  templateUrl: "./default-layout.component.html",
  styleUrls: ["./mycss.scss"],
})
export class DefaultLayoutComponent {
  public sidebarMinimized = false;

  public mynavItems: Array<any> = [];
  public curentRole: string;
  public checkaddmin: boolean = false;
  constructor(private router: Router) {

    // console.log('tetsstttssssssss'
    //   , navItems);

    let mylocal = JSON.parse(localStorage.getItem("userData"));
    const setlang = localStorage.getItem("language");
    if (setlang === 'lao') {
      this.mynavItems = laoNav
      console.log('lao languagse ', this.mynavItems);
      this.curentRole = mylocal.role;
      this.mynavItems = laoNav.filter((item) =>
        item.role.includes(this.curentRole)
      );
    } else {
      this.mynavItems = navItems
      this.curentRole = mylocal.role;
      this.mynavItems = navItems.filter((item) =>
        item.role.includes(this.curentRole)
      );
      console.log('english', this.mynavItems);

    }


    // item.role.split(",").findIndex((v) => v === this.curentRole) > -1
  }
  toggleMinimize(e) {
    this.sidebarMinimized = e;
  }
  logout() {
    console.log("logout ok ok");
    window.localStorage.removeItem("Token");
    window.localStorage.removeItem("user");
    this.router.navigate(["/login"]);
  }
}
